.service-page {
    &__hero {
        background-image: url(../img/service-page/hero.jpg);
        padding-top: 107px;
        padding-bottom: 72px;

        &-title {
            display: flex;
            justify-content: center;
            align-items: center;

            h2 {
                font-style: normal;
                font-weight: 600;
                font-size: 48px;
                line-height: 57px;
                text-align: center;
                letter-spacing: 0.02em;
            }
        }

        .main-calc__btn {
            margin-top: 31px;

            a {
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 19px;
                height: 81px;
                padding: 0 40px;

            }
        }
    }

    &__description {
        margin-top: 56px;
        margin-bottom: 64px;

        &-wp {
            display: flex;
            justify-content: space-between;
            position: relative;
            padding: 0 3% 0 5.5%;
        }

        &-left {
            width: 44%;
            margin-right: 10px;

            p {
                font-style: italic;
                font-weight: 500;
                font-size: 24px;
                line-height: 160%;
                color: #2C2F49;
            }

            .project-double {
                position: absolute;
                top: -31px;
                left: 14px;

                span {
                    height: 32px;
                    width: 13px;

                    &+span {
                        margin-left: 6px;
                    }
                }
            }
        }

        &-right {
            width: 45%;

            p {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 164%;
                color: #2C2F49;
            }
        }
    }

    &__offer {
        h2 {
            font-style: normal;
            font-weight: 600;
            font-size: 48px;
            line-height: 120%;
            color: #2C2F49;
            text-align: center;
        }

        &-grid {
            display: flex;
            flex: 1 1;
            // justify-content: space-between;
            margin-top: 65px;
        }

        &-el {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;

            &-title {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 164%;
                text-align: center;
                color: #878894;
                margin-top: 39px;
                max-width: 150px;
            }
        }
    }

    &__close {
        margin-top: 80px;
        padding-top: 40px;

        position: relative;

        &::before {
            content: "";
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            background: #F3F4F8;
            height: 264px;
            z-index: -1;
        }

        &-timer {
            display: flex;
            justify-content: space-between;
            background: linear-gradient(135deg, #4F5062 2.38%, #3A3A4A 67.04%);
            padding: 72px;
            margin-bottom: 64px;

            &-left,
            &-right {
                width: 43.6%;
            }

            &-wp {
                display: flex;
                justify-content: space-between;
            }

            &-el {}

            &-num {
                font-style: normal;
                font-weight: 300;
                font-size: 64px;
                line-height: 74px;
                color: #F39323;
            }

            &-mnth {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 19px;
                color: #FFFFFF;
            }
        }

        &-title {
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 29px;
            color: #ffffff;
            margin-bottom: 30px;
        }
    }

    &__control {

        &-wp {
            display: flex;
            justify-content: space-between;
        }

        &-el {
            width: 24%;
            max-width: 288px;
            position: relative;
            padding-top: 54px;
            display: flex;
            flex-direction: column;
            align-items: center;

            &::before {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 12px;
                height: 12px;
                background: #F39323;
            }

            &-title {
                font-style: normal;
                font-weight: 600;
                font-size: 24px;
                line-height: 120%;
                text-align: center;
                color: #2C2F49;
                margin-bottom: 16px;
            }

            &-text {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 163%;
                color: #878894;
                text-align: center;
                max-width: 250px;
            }
        }

        &-text {
            columns: 2;
            margin-top: 64px;
            column-gap: 11.3%;

            p {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 163%;
                color: #2C2F49;
                margin-bottom: 26px;
                width: 100%;
                break-inside: avoid;
            }
        }

        &+.main-request {
            padding-top: 120px;

            textarea {
                height: 65px;
                resize: none;

                @media (max-width: 760px) {
                    height: 99px;

                    &+label {
                        top: 3px;
                    }
                }
            }
        }
    }

    @media (max-width: 1239px) {
        &__hero {
            margin-top: 80px;
        }
    }

    @media (max-width: 990px) {
        &__description {
            padding: 0;
            margin-top: 96px;

            &-wp {
                display: block;
                padding: 0;
                padding-left: 72px;
            }

            &-left {

                width: 100%;
            }

            &-right {
                width: 100%;
                margin-top: 40px;
            }

        }

        &__offer {
            &-grid {
                flex-wrap: wrap;
                justify-content: space-around;
            }

            &-el {
                width: 30%;
                margin-bottom: 48px;

                &-title {
                    margin-top: 24px;
                }
            }

            h2 {
                font-size: 40px;
                line-height: 120%;
            }
        }

        &__close {
            padding-top: 48px;
            padding-bottom: 48px;
            margin-top: 16px;

            &:before {
                width: 69%;
                left: initial;
                height: 100%;
            }

            .container {
                padding-right: 0;
            }

            &-timer {
                display: block;
                padding-right: 30px;
                margin-bottom: 0;

                &-left,
                &-right {
                    width: 100%;
                }

                &-right {
                    margin-top: 64px;
                }

                &-wp {
                    justify-content: flex-start;
                }

                &-el {
                    margin-right: 13.7%;

                    &:last-of-type {
                        margin-right: 0;
                    }
                }
            }

            &-wp {
                width: 90.3%;
                margin-left: auto;
            }


        }

        &__control {
            margin-top: 40px;

            &-wp {
                flex-wrap: wrap;
                justify-content: space-between;
            }

            &-el {
                width: 50%;
                max-width: 336px;
                align-items: flex-start;
                padding-left: 48px;

                &::before {
                    top: 24px;
                }

                &-text {
                    text-align: left;
                }

                &-title {
                    text-align: left;
                }
            }

            &-text {
                column-gap: 6.6%;

                p {
                    width: 99%;
                }
            }

            &+.main-request {
                padding-top: 80px;
            }
        }


        &__hero {
            padding-top: 78px;
            padding-bottom: 73px;
        }

        &__description {
            &-wp {}
        }




    }

    @media (max-width: 767px) {
        &__hero {
            margin-top: 56px;
        }
    }

    @media (max-width: 760px) {

        &__close {
            &-wp {
                width: 100%;
            }

            &-timer {
                padding: 24px;

                &-num {
                    font-weight: 300;
                    font-size: 32px;
                    line-height: 37px;
                }

                &-mnth {
                    font-weight: 500;
                    font-size: 9px;
                    line-height: 11px;
                }
            }

            &-title {
                font-size: 16px;
                line-height: 19px;
            }
        }

        &__control {
            &-text {
                columns: initial;
            }

            &-el {
                &-title {
                    font-size: 16px;
                    line-height: 120%;
                    margin-bottom: 9px;
                }

                &-text {
                    font-size: 12px;
                    line-height: 160%;
                }
            }
        }


    }

    @media (max-width: 575px) {

        &__hero {
            padding-top: 24px;
            padding-left: 16px;
            padding-right: 16px;
            padding-bottom: 25px;

            .main-calc__btn {
                margin-top: 26px;
                height: 85px;

                a {
                    font-size: 14px;
                    line-height: 17px;
                    text-align: center;
                    padding: 0 25px;
                    height: auto;
                }
            }
        }

        &__description {
            margin-top: 55px;

            &-wp {
                padding-left: 0;
            }

            &-left {
                padding-left: 30px;

                .project-double {
                    top: -14px;
                    left: 8px;

                    span {
                        height: 16px;
                        width: 5px;
                    }
                }

                p {
                    font-size: 16px;
                    line-height: 164%;
                }
            }

            &-right {
                margin-top: 25px;

                p {
                    font-size: 12px;
                    line-height: 160%;
                }
            }
        }

        &__offer {
            h2 {
                font-size: 24px;
                line-height: 120%;
            }

            &-grid {
                margin-top: 40px;
                justify-content: space-between;
            }

            &-el {
                width: 47%;
                margin-bottom: 25px;

                svg {
                    height: 48px;
                    width: 48px;
                }

                &-title {
                    font-size: 12px;
                    line-height: 160%;
                    margin-top: 16px;
                }
            }
        }

        &__control {
            margin-top: 20px;

            &-el {
                width: 100%;
                padding-top: 8px;
                padding-left: 24px;
                padding-right: 10px;
                padding-right: 25px;

                margin-bottom: 23px;

                &:last-of-type {
                    margin-bottom: 0;
                }

                &::before {
                    top: 0;
                    width: 8px;
                    height: 8px;
                }
            }

            &-text {
                margin-top: 40px;

                p {
                    width: 100%;
                    font-size: 12px;
                    line-height: 160%;
                    margin-bottom: 16px;
                }
            }

            &+.main-request {
                padding-top: 47px;
                padding-bottom: 42px;
            }
        }


        &__close {
            padding-top: 24px;
            margin-top: 15px;
            padding-bottom: 20px;

            &:before {
                width: 100%;
                height: 60%;
            }

            &-timer {
                padding-right: 23px;
                padding-bottom: 30px;

                &-num {
                    font-weight: 300;
                    font-size: 32px;
                    line-height: 37px;
                }

                &-mnth {
                    font-weight: 500;
                    font-size: 9px;
                    line-height: 11px;
                    margin-top: 6px;
                }

                &-right {
                    margin-top: 27px;
                }

                &-wp {
                    justify-content: space-between;
                }

                &-el {
                    margin-right: 0;
                }
            }

            &-title {
                margin-bottom: 16px;
            }
        }
    }


}
