.case-page {
    &__hero {
        background-image: url(../img/case-page/hero.jpg);
        padding-left: 16px;
        padding-right: 16px;
    }

    &__top {

        .breadcrumbs {
            padding-top: 16px;
        }

        &-line {
            display: flex;
            justify-content: space-between;
            padding-bottom: 14px;
        }


        &-left {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            padding-top: 16px;
        }

        &-prev,
        &-next {
            height: 56px;
            border: 2px solid #F3F4F8;
            transition: 0.25s;
            padding-left: 23px;
            padding-right: 20px;
            display: flex;
            align-items: center;
            color: #2C2F49;
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 19px;
            letter-spacing: 0.02em;

            svg {
                fill: #2C2F49;
                transition: .25s;
            }

            @media (min-width: 761px) {
                &:hover {
                    color: #fff;
                    background: #EC663B;

                    svg {
                        fill: #fff;
                    }
                }
            }
        }

        &-prev {
            border-right: none;

            span {
                margin-left: 15px;
                display: block;
            }
        }

        &-next {
            span {
                margin-right: 15px;
                display: block;
            }
        }
    }

    &__main {
        padding-bottom: 120px;

        &-wp {
            display: flex;
            justify-content: space-between;
        }

        h4 {
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 160%;
            color: #2C2F49;
        }

        article {
            margin-top: 38px;
        }

        ul {
            padding-left: 72px;
            padding-top: 42px;
            padding-bottom: 37px;
            border-bottom: 2px solid #F3F4F8;


            li {
                list-style: none;
                display: flex;
                margin-top: 33px;

                &:first-of-type {
                    margin-top: 0;
                }
            }
        }

        .list-date {
            line-height: 120%;
            font-size: 15px;
            font-weight: 600;
            color: #C3C4D4;
            min-width: 130px;
            margin-right: 14px;

            span {
                font-size: 40px;
                margin-right: 9px;
            }
        }

        .list-text {
            padding-top: 4px;

            p {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 163%;
                color: #2C2F49;
                margin-bottom: 17px;

                &:last-of-type {
                    margin-bottom: 0;
                }
            }

            strike {
                color: #F39323;
            }
        }

        blockquote {
            margin-top: 24px;
            padding-left: 72px;
            padding-top: 32px;
            position: relative;
            padding-bottom: 40px;
            border-bottom: 2px solid #F3F4F8;


            p {
                margin-bottom: 38px;
                font-style: italic;
                font-weight: 500;
                font-size: 24px;
                line-height: 160%;
                color: #2C2F49;

                &:last-of-type {
                    margin-bottom: 0;
                }
            }
        }

        .cases__item-bottom-left {
            display: flex;
            align-items: flex-end;
            flex-direction: column;
            margin-top: 40px;
            margin-right: 0;
        }


    }

    &__left {
        width: 66.6%;

        &-top {
            display: flex;
            justify-content: space-between;
            width: 100%;
            padding-bottom: 18px;
            border-bottom: 2px solid #F3F4F8;
            margin-bottom: 0;
        }

        &-share {
            padding-top: 3px;

            svg {
                cursor: pointer;

                path {
                    transition: 0.25s;
                }

                @media (min-width: 767px) {
                    &:hover {
                        // stroke: #F39323;

                        path {
                            fill: #F39323;
                        }
                    }
                }
            }


        }

        .project-double {
            position: absolute;
            top: 0;
            left: 17px;

            span {
                height: 32px;
                width: 11px;
            }
        }

        &-date {
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #F39323;
        }
    }

    &__right {
        width: 22.3%;

        .project-double {
            justify-content: initial;
            padding-left: 13px;

            span {
                height: 25px;
                width: 8px;
            }
        }

        &-wp {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }

        &-title {
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 29px;
            letter-spacing: 0.02em;
            color: #2C2F49;
            margin-top: 15px;
        }

        &-list {
            padding-top: 32px;

            a {
                display: block;
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 19px;
                color: #878894;
                margin-bottom: 16px;
                transition: color 0.25s;

                &:last-of-type {
                    margin-bottom: 0;
                }

                @media (min-width: 761px) {
                    &:hover {
                        color: #EC663B;
                    }
                }
            }
        }

        .cases__link {
            position: relative;
            height: 64px;
            display: flex;
            align-items: center;
            padding: 0 46px 1px 34px;
            margin-top: 40px;

            a {}

            svg {
                transition: 0.25s;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }

            @media (min-width: 761px) {
                &:hover {
                    span {
                        color: #fff;
                    }


                    svg {
                        path {
                            fill-opacity: initial;
                            fill: #EC663B;
                            stroke: #EC663B;
                        }
                    }
                }
            }
        }

    }

    &__bottom-btns {
        align-items: center;
        justify-content: center;
        margin-top: 64px;
        display: none;
    }


    @media (max-width: 1140px) {

        &__hero {
            .services__hero-title {

                h2 {
                    font-size: 40px;
                    line-height: 120%;
                    text-align: left;
                    width: 57%;
                }
            }
        }



        &__left {
            width: 71%;
        }

        &__right {
            width: 25%;
        }
    }

    @media (max-width: 1040px) {

        &__hero {
            .services__hero-title {
                align-items: flex-start;
            }

            .project-double {
                margin-top: 12px;
            }
        }

        &__top {
            display: block;

            &-line {
                justify-content: center;
                padding-left: 0;
            }

            .breadcrumbs {
                display: none;
            }
        }
    }


    @media (max-width: 990px) {

        &__hero {
            padding-top: 77px;
            padding-bottom: 75px;
        }

        &__top {
            display: block;
        }

        &__left {
            width: 100%;

            &-share {
                margin-right: 8px;
                padding-top: 1px;
            }

            &-top {
                padding-bottom: 20px;
            }
        }

        &__main {
            padding-bottom: 63px;

            &-wp {
                flex-wrap: wrap;
            }

            ul {
                padding-left: 0;

                li {
                    display: block;
                }
            }

            .list-date {
                br {
                    display: none;
                }
            }

            .list-text {
                margin-top: 8px;
                padding-left: 72px;
            }
        }

        &__right {
            width: 100%;
            display: none;
        }

        &__article {
            margin-top: 40px;

            .cases__item-name {
                font-size: 16px;
                line-height: 19px;
            }

            .cases__item-rank {
                font-size: 16px;
                line-height: 160%;
            }

            .cases__item-bottom-left {
                margin-right: 0;
            }

        }

        &__bottom-btns {
            display: flex;
        }
    }



    @media (max-width: 575px) {

        &__main {
            padding-bottom: 39px;
        }

        &__hero {
            padding-top: 12px;
            padding-bottom: 41px;

            .services__hero-title {
                align-items: center;

                h2 {
                    font-weight: 600;
                    font-size: 24px;
                    line-height: 120%;
                    width: 90%;
                    text-align: center;
                }
            }
        }

        &__top {
            display: none;

            &-prev,
            &-next {
                font-size: 14px;
                line-height: 17px;
                height: 50px;
                text-align: center;

                svg {
                    width: 20px;
                }
            }

            &-prev {
                padding-left: 10px;
                padding-right: 10px;

                span {
                    margin-left: 13px;
                }
            }

            &-next {
                padding-left: 10px;
                padding-right: 10px;

                span {
                    margin-right: 13px;
                }
            }
        }

        &__left {
            margin-top: 23px;

            &-date {
                font-size: 12px;
                line-height: 14px;
            }

            &-top {
                padding-bottom: 11px;
                border-width: 1px;
            }

            .project-double {
                left: 8px;

                span {
                    height: 16px;
                    width: 6px;
                    border-radius: 2px 0;

                    &+span {
                        margin-left: 4px;
                    }
                }
            }
        }

        &__article {
            margin-top: 24px;
        }

        &__main {
            blockquote {

                padding-left: 24px;
                padding-top: 16px;
                padding-bottom: 23px;
                border-width: 1px;

                p {
                    font-size: 16px;
                    line-height: 164%;
                    margin-bottom: 26px;
                }
            }

            article {
                margin-top: 24px;
            }

            h4 {
                font-size: 16px;
                line-height: 19px;
            }

            ul {
                padding-top: 19px;
                padding-bottom: 22px;

                .list-date {
                    font-size: 12px;
                    margin-right: 0;

                    span {
                        font-size: 24px;
                        line-height: 140%;
                        margin-right: 0;
                    }
                }

                .list-text {
                    margin-top: 0px;
                    padding-left: 24px;

                    p {
                        font-size: 12px;
                        line-height: 160%;
                        margin-bottom: 16px;
                    }
                }

                li {
                    margin-top: 11px;
                }
            }

            .cases__item-bottom-left {
                margin-top: 23px;
            }

            .cases__item {
                &-name {
                    font-size: 12px;
                    line-height: 14px;
                }

                &-rank {
                    font-size: 12px;
                    line-height: 160%;
                }
            }
        }

        &__bottom-btns {
            margin-top: 39px;
        }



    }






}