.page-404 {
    &__wp {
        margin-top: 96px;
        padding-top: 94px;
        display: flex;
        align-items: center;
        flex-direction: column;
        padding-bottom: 99px;
    }

    &__img {
        position: relative;
        width: 100%;

        &-wp {
            max-width: 790px;
            margin-left: auto;
            margin-right: auto;
        }

        &::before {
            content: "";
            width: 100%;
            height: 170px;
            position: absolute;
            bottom: -73px;
            left: 0;
            background: radial-gradient(50% 50% at 50% 50%, #C3C4D4 0%, rgba(195, 196, 212, 0) 100%);
            opacity: 0.8;
            z-index: -1;
        }

        img {
            width: 100%;
        }

        &:after {
            content: '  ';
            position: absolute;
            bottom: 19%;
            left: 50%;
            transform: translateX(-50%);
            font-style: normal;
            font-weight: 800;
            font-size: 24px;
            line-height: 30px;
            color: #fff;
            width: 100%;
            text-align: center;
        }
    }

    p {
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 140%;
        text-align: center;
        color: #2C2F49;
        margin-top: 59px;
        max-width: 645px;
    }

    .main-calc__btn {
        margin-top: 63px;

        a {
            color: #EC663B;
            padding: 0 75px;
            height: 85px;
            transition: 0.25s;

            & path {
                fill-opacity: 0;
            }
        }

        @media (min-width: 761px) {
            &:hover {
                a {
                    color: #fff;
                }

                svg {
                    path {
                        fill-opacity: 1;
                        fill: #EC663B;
                        stroke: #EC663B;
                    }

                }
            }
        }
    }

    @media (max-width: 1239px) {
        &__wp {
            margin-top: 80px;
        }
    }

    @media (max-width: 990px) {

        &__wp {
            padding-top: 181px;
            padding-bottom: 180px;
        }

        &__img {
            width: 80.2%;

            &:after {
                font-size: 28px;
                line-height: 35px;
                font-weight: 900;
                bottom: 15%;
            }
        }

        p {
            width: 69%;
            line-height: 142%;
            margin-top: 58px;
        }

        .main-calc__btn {
            margin-top: 61px;
        }
    }

    @media (max-width: 767px) {
        &__wp {
            margin-top: 56px;
        }
    }

    @media (max-width: 575px) {
        &__wp {
            padding-top: 73px;
            padding-bottom: 70px;
        }

        &__img {
            width: 100%;

            &::after {
                font-size: 16px;
                line-height: 20px;
                bottom: 18%;
            }
        }

        p {
            font-size: 16px;
            line-height: 140%;
            margin-top: 35px;
            width: 90%;
        }

        .main-calc__btn {
            margin-top: 66px;

            a {
                font-size: 14px;
                line-height: 17px;
                padding: 0 48px;
            }

        }


    }


}
