@mixin font($f-name, $f-style, $f-weight, $f-url) {
	@font-face {
	  font-family: $f-name;
	  font-style:  $f-style;
	  font-weight: $f-weight;
	  src: url('../fonts/#{$f-url}.woff') format('woff'),
	  url('../fonts/#{$f-url}.ttf') format('truetype'),
	  url('../fonts/#{$f-url}.eot'),
	  url('../fonts/#{$f-url}.eot?#iefix') format('embedded-opentype');
	}
}

@include font('Gilroy', normal, 300, 'Gilroy-Light');
@include font('Gilroy', normal, 400, 'Gilroy-Regular');
@include font('Gilroy', normal, 500, 'Gilroy-Medium');
@include font('Gilroy', italic, 500, 'Gilroy-MediumItalic');
@include font('Gilroy', normal, 600, 'Gilroy-SemiBold');
@include font('Gilroy', normal, 700, 'Gilroy-Bold');
@include font('Gilroy', normal, 800, 'Gilroy-ExtraBold');
@include font('Gilroy', normal, 900, 'Gilroy-Heavy');
