.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $headerHeight;
  z-index: 500;
  border-bottom: 1px solid #C3C4D4;
  display: flex;
  padding-right: 96px;
  @include transition;

  &.white,
  &.scrolled {
    background: white;


    & .header {


      &__services {
        background: rgba(243, 244, 248, 0.6);
        backdrop-filter: none;

        &>span {

          & span {
            background: #2C2F49;
          }
        }

        &:hover {
          &>span span {
            background: #EC663B;
          }
        }
      }

      &__logo {

        &-text {

          color: #878894;
        }
      }

      &__nav {

        & a {

          &.active {

            & span {
              color: #2C2F49;
            }
          }

          &:hover {

            & span {
              color: #EC663B;
            }
          }

          & span {
            color: rgba(44, 47, 73, 0.7);
          }
        }
      }

      &__tel,
      &__calc {


        &:hover {

          & span {
            color: #EC663B;
          }

          & svg {
            fill: #EC663B;
          }
        }

        & svg {
          fill: #2C2F49;
        }

        & span {
          color: #2C2F49;
        }
      }

      &__left {
        background: rgba(243, 244, 248, 0.6);
        backdrop-filter: none;

        & a {
          color: #2C2F49;

          &:hover {
            color: #EC663B;
          }
        }
      }
    }
  }

  &.whiteLeft {

    & .header__services,
    & .header__left {
      background: white;
    }
  }

  &__services {
    width: $headerHeight;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(16px);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1px 20px 0;
    height: 100%;
    position: relative;
    @include transition('background');

    &:hover {

      &>span span {
        background: #EC663B;
      }
    }

    &.close {
      &>span {

        &:before,
        &:after {
          opacity: 1;
          transition-delay: .2s;
          height: 2px;
        }

        &:before {
          transform: translate(-50%, -50%) rotate(45deg);
        }

        &:after {
          transform: translate(-50%, -50%) rotate(-45deg);
        }

        & span {
          opacity: 0;
          transform: translateY(100%);
          background: #2C2F49;

          &:nth-of-type(n+4) {
            transform: translateY(-100%);
          }

          &:nth-of-type(1) {
            transform: translate(100%, 100%);
          }

          &:nth-of-type(3) {
            transform: translate(-100%, 100%);
          }

          &:nth-of-type(4) {
            transform: translate(100%, -100%);
          }

          &:nth-of-type(6) {
            transform: translate(-100%, -100%);
          }
        }
      }
    }

    &>span {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      position: relative;
      z-index: 1;

      &:before,
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 22px;
        height: 8px;
        background: #2C2F49;
        border-radius: 8px;
        margin: 0;
        opacity: 0;
        @include transition;
        transform: translate(-50%, -50%);
        transform-origin: center;
      }

      & span {
        flex: 0 0 8px;
        width: 8px;
        height: 8px;
        background: white;
        margin: 3.5px;
        @include transition;
      }
    }
  }

  &__logo {
    display: flex;
    align-items: center;
    width: 231px;
    margin-left: 144px;
    padding-top: 17px;

    &-text {
      margin-left: 23px;
      font-weight: bold;
      font-size: 11px;
      line-height: 160%;
      letter-spacing: 0.08em;
      text-transform: uppercase;
      color: #FFFFFF;
      @include transition;
    }

    @media (min-width: 1701px) {
      margin-right: 68px;
    }
  }

  &__nav {
    display: flex;
    align-items: center;
    height: calc(100% + 1px);
    margin: 0 auto;

    & a {
      display: flex;
      align-items: center;
      height: 100%;
      border-bottom: 2px solid transparent;
      padding-top: 9px;
      margin-right: 64px;
      @include transition;

      &:last-of-type {
        margin-right: 0;
      }

      &.active {
        border-bottom-color: #EC663B;

        & span {
          color: white;
        }
      }

      &:hover {
        border-bottom-color: #EC663B;

        & span {
          color: #EC663B;
        }

      }

      & span {
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        letter-spacing: 0.025em;
        color: rgba(255, 255, 255, 0.7);
        @include transition;
      }
    }
  }

  &__tel {
    display: flex;
    align-items: center;
    padding-top: 7px;

    &:hover {

      & span {
        color: #EC663B;
      }

      & svg {
        fill: #EC663B;
      }
    }

    & svg {
      fill: white;
      @include transition;
    }

    & span {
      margin-left: 8px;
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      color: #FFFFFF;
      @include transition;
    }
  }

  &__calc {
    margin-left: 96px;
    display: flex;
    align-items: center;
    padding-top: 8px;
    position: relative;

    &.close {

      &:before,
      &:after {
        opacity: 1;
      }

      &:before {
        transform: translateY(-50%) rotate(45deg);
      }

      &:after {
        transform: translateY(-50%) rotate(-45deg);
      }

      & span {
        opacity: 0;
      }

      & svg {
        opacity: 0;
      }
    }

    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      width: 22px;
      height: 2px;
      background: #2C2F49;
      border-radius: 8px;
      opacity: 0;
      margin-top: 4px;
      margin-right: -3px;
      @include transition;
      transform: translateY(-50%);
      transform-origin: center;
    }

    &:hover {

      &:before,
      &:after {
        background: #EC663B;
      }

      & span {
        color: #EC663B;
      }

      & svg {
        fill: #EC663B;
      }
    }

    & span {
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      text-align: right;
      letter-spacing: 0.02em;
      color: #FFFFFF;
      @include transition;
    }

    & svg {
      fill: white;
      margin-left: 19px;
      @include transition;
    }
  }

  &__left {
    position: fixed;
    top: $headerHeight;
    left: 0;
    width: $headerHeight;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(16px);
    height: calc(100vh - 96px);
    display: flex;
    flex-direction: column;
    padding-top: 84px;
    padding-bottom: 32.2vh;
    align-items: center;
    @include transition('background');

    &-social {
      display: flex;
      margin-top: auto;
      transform: rotate(-90deg);
      transform-origin: center;

      & a {

        &:first-of-type {
          margin-right: 64px;
        }
      }
    }

    & a {
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      letter-spacing: 0.04em;
      color: #FFFFFF;
      @include transition;

      &:hover {
        color: #EC663B;
      }
    }

    &>a {
      transform: rotate(-90deg);
      transform-origin: center;
    }
  }

  &__showmobile {
    display: none;
  }
}

.main-menu {
  position: fixed;
  background: white;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 490;
  padding: 192px $headerHeight 88px 240px;
  display: grid;
  grid-template-columns: 1fr 144px 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  opacity: 0;
  visibility: hidden;
  @include transition;

  &.active {
    opacity: 1;
    visibility: visible;
  }

  &__item {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    padding: 104px 48px 48px;
    overflow: hidden;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      background: linear-gradient(45deg, #F39324 14.46%, #EC663B 95.71%);
      opacity: 0;
      visibility: hidden;
      @include transition;
    }

    &:hover {

      &:after {
        opacity: 1;
        visibility: visible;
      }

      & .main-menu {

        &__number {
          color: white;

          &:after {
            background: white;
          }
        }

        &__icon {


          & svg {

            & path,
            & rect {
              fill: white;

              &.no-fill {
                fill-opacity: 0;
              }
            }
          }
        }

        &__text {
          color: white;
        }
      }
    }


    &:first-of-type {
      grid-row: 1/4;
      padding-bottom: 72px;
      display: flex;
      flex-direction: column;
      background: linear-gradient(135deg, #4F5062 2.38%, #3A3A4A 67.04%);


      & .main-menu {

        &__number {
          top: 48px;
          padding-right: 25px;

          &:after {
            background: white;
          }
        }

        &__icon {
          margin-top: auto;

          & svg {
            height: 108px;
          }
        }

        &__text {
          margin-top: 102px;
          color: white;
          padding-right: 55px;
        }
      }
    }
  }

  &__number {
    position: absolute;
    top: 52px;
    right: 48px;
    padding-right: 24px;
    padding-bottom: 2px;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: #F39323;
    display: flex;
    align-items: center;
    height: 24px;
    @include transition;


    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 1px;
      height: 100%;
      border-radius: 8px;
      background: #2C2F49;
      @include transition;
    }
  }

  &__icon {
    display: flex;
    align-items: center;

    & svg {
      height: 64px;
    }
  }

  &__text {
    margin-top: 40px;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    color: #2C2F49;
    padding-right: 50px;
    @include transition;
  }
}

.modal-calc {
  position: fixed;
  background: white;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 490;
  @include transition;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: $headerHeight;
  padding-bottom: 42px;
  opacity: 0;
  visibility: hidden;

  &.active {
    opacity: 1;
    visibility: visible;
  }

  &__title {
    font-weight: 600;
    font-size: 48px;
    line-height: 57px;
    text-align: center;
    color: #2C2F49;
    margin-top: 24px;
    padding: 0 150px;
  }

  &__form {
    margin-top: 64px;

    & .main-calc {

      &__type {

        &-wp {
          background: #F3F4F8;
        }
      }

      &__row {
        border: 2px solid #C3C4D4;
        border-radius: 8px;
      }

      &__input {
        border-right-color: #C3C4D4;

        & input {

          &:disabled {
            background: #F3F4F8;
            backdrop-filter: none;

            &~label {
              color: #878894;
            }
          }
        }

        & .select2-container {

          &--disabled {

            & .select2-selection {
              background: #F3F4F8;
              backdrop-filter: none;
            }

            &~label {
              color: #878894;
            }
          }
        }
      }

      &__btn {

        & button {
          color: white;
          @include transition;

          &:disabled {
            color: #C3C4D4;

            & path {
              fill: #F3F4F8;
              fill-opacity: 1;
              stroke: #F3F4F8;
            }

            &:hover {
              color: #C3C4D4;

              & path {
                fill: #F3F4F8;
                fill-opacity: 1;
                stroke: #F3F4F8;
              }
            }
          }

          &:hover {
            color: white;
          }
        }
      }
    }
  }
}

.main-modal {
  position: fixed;
  z-index: 5000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;

  &.active {
    visibility: visible;

    & .main-modal__overlay {
      opacity: 1;
      visibility: visible;
    }

    & .main-modal__content {
      opacity: 1;
      visibility: visible;
      transform: none;
    }
  }

  &__overlay {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(24px);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    @include transition;
    opacity: 0;
    visibility: hidden;
  }

  &__content {
    position: relative;
    background: white;
    width: 100%;
    max-width: 432px;
    box-shadow: 0 4px 80px rgba(44, 47, 73, 0.6);
    padding: 72px;
    @include transition;
    opacity: 0;
    visibility: hidden;
    transform: scale(.7) translateY(-30px);

    & .project-double {
      position: absolute;
      top: -17px;
      left: 93px;
    }
  }

  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    &:after {
      content: '';
      position: absolute;
      width: 40px;
      height: 40px;
      top: -8px;
      left: -8px;
    }

    &:hover span {

      &:after,
      &:before {
        background: #EC663B;
      }
    }

    & span {
      width: 22px;
      height: 2px;
      position: relative;

      &:before,
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #2C2F49;
        transform-origin: center;
        @include transition;
      }

      &:before {
        transform: rotate(45deg);
      }

      &:after {
        transform: rotate(-45deg);
      }
    }
  }
}

.modal-messenger {

  &__title {
    font-weight: 600;
    font-size: 48px;
    line-height: 57px;
    color: #2C2F49;
  }

  &__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 160%;
    color: #2C2F49;
    white-space: nowrap;
    margin-top: 24px;

    & a {
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }
  }

  &__img {
    display: flex;
    align-items: flex-start;
    margin-top: 40px;
  }
}

.mobile-menu {
  display: none;
}

.modal-ty {

  &__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: #2C2F49;
  }

  &__text {
    margin-top: 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: #878894;
  }

  &__img {
    display: none;
  }
}

.modal-oops {

  &__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: #2C2F49;
  }

  &__text {
    margin-top: 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: #878894;
  }

  &__form {

    & .main-request__input {
      margin-bottom: 0;
      margin-top: 20px;

      &:nth-of-type(2n) {
        margin-left: 0;
      }
    }
  }

  & .result-form__btn {
    width: 100%;
    margin-left: 0;
    padding: 0 20px 2px 0;
    justify-content: center;
    margin-top: 40px;

    &:before,
    &:after {
      transform: rotateX(-14deg) skewX(-10deg);
    }
  }
}

@media (max-height: 845px) {
  .main-menu {
    padding-top: $headerHeight + 20px;
    padding-bottom: 20px;
  }
}

@media (max-height: 720px) {
  .main-menu {

    &__item {
      display: flex;
      flex-direction: column;
      padding: 30px;
    }

    &__icon {
      margin-top: auto;
    }

    &__text {
      margin-top: 20px;
    }
  }
}

@media (max-width: 1700px) {
  .header {
    padding-right: 40px;

    &__logo {
      margin-left: 40px;
    }

    &__nav {

      & a {
        margin-right: 30px;
      }
    }

    &__calc {
      margin-left: 30px;
    }
  }

  .main-menu {
    padding-left: $headerHeight + 40px;
    padding-right: 40px;
    grid-template-columns: 1fr 20px 1fr 1fr 1fr;

    &__item {

      &:first-of-type {

        & .main-menu__text {
          padding-right: 0;
        }
      }
    }

    &__text {
      padding-right: 0;
    }
  }
}

@media (max-width: 1400px) {
  .main-menu {

    &__item {
      padding: 104px 20px 20px;
    }
  }
}

@media (max-width: 1239px) {

  $headerHeight: 80px;

  .header {
    height: $headerHeight;
    padding: 0;
    border-bottom-color: rgba(195, 196, 212, 0.4);

    &.scrolled,
    &.white {


      & .header {

        &__services {
          background: transparent;
        }

        &__showmobile {

          & span {
            background: #2C2F49;
          }
        }
      }
    }

    &.open-menu {
      background: #F3F4F8;
    }

    &.hide-all {

      & .header__services,
      & .header__showmobile {
        display: none;
      }
    }

    &__closeCalc {
      width: 32px;
      height: 32px;
      top: 50%;
      right: 24px;
      transform: translateY(-50%);
      position: absolute;

      &:after,
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 33px;
        height: 3px;
        background: #2C2F49;
        border-radius: 8px;
        margin: 0;
        @include transition;
        transform: translate(-50%, -50%);
        transform-origin: center;
      }


      &:after {
        transform: translate(-50%, -50%) rotate(45deg);
      }

      &:before {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }

    &__services {
      width: 87px;
      background: transparent;
      backdrop-filter: none;

      &.hide {
        opacity: 0;
        visibility: hidden;
      }

      &.close {

        &~.header__showmobile {
          opacity: 0;
          visibility: hidden;
        }
      }

      &>span {

        &:before,
        &:after {
          width: 32px;
          margin-left: -4px;
        }
      }
    }

    &__left {
      display: none;
    }

    &__tel,
    &__calc,
    &__nav {
      display: none;
    }

    &__logo {
      width: auto;
      margin-left: auto;
      margin-right: auto;
      padding-top: 0;

      & img {
        height: 40px;
      }

      &-text {
        display: none;
      }
    }

    &__showmobile {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-end;
      width: 86px;
      padding-right: 24px;

      & span {
        background: white;
        height: 4px;
        width: 40px;
        border-radius: 0 0 0 5px;
        transform-origin: center;
        @include transition;

        &:last-of-type {
          margin-top: 8px;
          width: 24px;
        }
      }
    }
  }

  .main-menu {
    padding: $headerHeight 24px 0;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;

    &>div {
      display: none;
    }

    &__item {
      padding: 45px 48px;

      &:first-of-type {
        grid-row: 1/3;
        order: 1;
        margin-top: 24px;
        height: calc(100% - 24px);
        padding-bottom: 45px;

        & .main-menu__number {
          top: auto;
          bottom: 160px;
        }

        & .main-menu__icon {

          & svg {
            height: 64px;
          }
        }

        & .main-menu__text {
          margin-top: 21px;
        }
      }

      &:nth-of-type(2) {
        order: 4;
      }

      &:nth-of-type(3) {
        order: 6;
      }

      &:nth-of-type(4) {
        order: 2;
      }

      &:nth-of-type(5) {
        order: 3;
      }

      &:nth-of-type(6) {
        order: 5;
      }

      &:nth-of-type(7) {
        order: 7;
      }
    }

    &__number {
      top: 49px;
      padding-right: 23px;
    }

    &__icon {

      & svg {
        height: 64px;
        width: 64px;
      }
    }

    &__text {
      margin-top: 24px;
    }
  }

  .mobile-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    visibility: hidden;
    display: block;
    z-index: 50000;

    &.active {
      visibility: visible;

      & .mobile-menu {

        &__overlay {
          opacity: 1;
        }

        &__content {
          transform: translateX(0);
        }
      }
    }

    &__overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(44, 47, 73, 0.4);
      backdrop-filter: blur(14px);
      opacity: 0;
      @include transition;
    }

    &__content {
      position: absolute;
      top: 0;
      right: 0;
      width: 480px;
      height: 100%;
      padding-bottom: 20px;
      overflow: auto;
      background: white;
      transform: translateX(110%);
      @include transition;
    }

    &__top {
      height: $headerHeight;
      border-bottom: 1px solid rgba(195, 196, 212, 0.4);
      display: flex;
      align-items: center;
      padding: 0 24px 0 50px;
    }

    &__logo {
      display: flex;

      & img {
        height: 40px;
      }
    }

    &__close {
      width: 32px;
      height: 32px;
      margin-left: auto;
      position: relative;

      &:after {
        content: '';
        position: absolute;
        width: 60px;
        height: 60px;
        top: -14px;
        left: -14px;
        opacity: 0;
      }

      & span {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 33px;
        height: 3px;
        background: #2C2F49;
        border-radius: 8px;
        margin: 0;
        @include transition;
        transform: translate(-50%, -50%);
        transform-origin: center;

        &:first-of-type {
          transform: translate(-50%, -50%) rotate(45deg);
        }

        &:nth-of-type(2) {
          transform: translate(-50%, -50%) rotate(-45deg);
        }
      }
    }

    &__nav {

      & a {
        display: flex;
        align-items: center;
        padding-left: 50px;
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
        letter-spacing: 0.02em;
        color: #2C2F49;
        padding-right: 30px;
        height: 96px;
        border-bottom: 1px solid rgba(195, 196, 212, 0.4);
        position: relative;

        &:after {
          content: '';
          position: relative;
          background: url(../img/main/arrow-mobile.svg) center no-repeat;
          width: 16px;
          height: 11px;
          margin-left: auto;
          margin-top: 2px;

        }
      }
    }

    &__contact {
      padding: 80px 53px 44px;

      & a {
        display: flex;
        align-items: center;
        margin-bottom: 32px;

        &:first-child {

          & i {
            margin-left: 0;
            flex: 0 0 46px;
            width: 46px;
          }

          & span {
            letter-spacing: normal;
            text-decoration: none;
          }
        }

        &:last-child {
          margin-bottom: 0;
        }

        & i {
          display: flex;
          align-items: center;
          flex: 0 0 49px;
          width: 49px;
          margin-left: -3px;

          & svg {
            fill: #2C2F49;
          }
        }

        & span {
          font-weight: 600;
          font-size: 24px;
          line-height: 29px;
          display: flex;
          align-items: center;
          letter-spacing: 0.04em;
          text-decoration-line: underline;
          color: #2C2F49;
        }
      }
    }

    &__btn {
      display: inline-flex;
      height: 64px;
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      align-items: center;
      text-align: center;
      color: #FFFFFF;
      border-radius: 7px 0 35px 7px;
      padding: 0 38px 1px 31px;
      position: relative;
      z-index: 1;
      perspective: 200px;
      margin-left: 51px;
      @include transition;

      & i {
        display: flex;
        margin-right: 12px;

        & svg {
          fill: white;
        }
      }

      &:after {
        z-index: -2;
        background: linear-gradient(94.22deg, #F39324 -14.8%, #EC663B 106.65%);
        transform: rotateX(-9deg) skewX(-7deg);
        transform-origin: left top;
        border-radius: 8px 0 16px 8px;
        height: 104%;
        width: 100%;
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        @include transition;
      }
    }

  }

  .modal-calc {
    overflow: auto;
    align-items: flex-start;

    & .container {
      margin-top: 0;
    }

    & .project-double {
      display: none;
    }

    &__title {
      font-size: 40px;
      line-height: 48px;
      text-align: left;
      padding: 40px 0 0 72px;
      width: 460px;
      max-width: 100%;
    }

    &__form {
      margin-top: 46px;
      padding-left: 72px;

      & .main-calc {

        &__type {
          justify-content: flex-start;
        }

        &__radio {
          min-width: 120px;
          width: 120px;

          & span {
            font-size: 16px;
            line-height: 19px;
            display: flex;
            justify-content: center;
            height: 48px;
          }
        }

        &__row {
          flex-direction: column;
          border: none;
          margin-top: 40px;
        }

        &__input {
          flex: auto !important;
          border-right: 0;
          width: 432px;
          height: 67px;
          margin-bottom: 24px;

          &.main-calc__select {

            &:after {
              display: none;
            }
          }

          &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 10px;
            border: 2px solid #C3C4D4;
            border-top: 0;
          }

          & .select2-container {

            &:after {
              content: '';
              position: absolute;
              bottom: 0;
              left: 0;
              width: 100%;
              height: 10px;
              border: 2px solid #C3C4D4;
              border-top: 0;
            }
          }

          &.cont {

            &:after {
              border-color: #2C2F49;
            }

            & label {
              top: 6px;
            }
          }

          & .select2-selection {
            padding: 0 16px 8px;
            box-shadow: none !important
          }

          & .main-calc__input select.cont~label {
            top: 6px;
          }

          & .select2-container--default .select2-selection--single .select2-selection__rendered {
            font-size: 24px;
            line-height: 29px;
          }

          & .select2-container--disabled .select2-selection {
            background: transparent;
          }

          & .select2-container--disabled~label {
            color: #C3C4D4;
          }

          & select.cont {

            &~label {
              top: 6px;
            }

            &~.select2-container:after {
              border-color: #2C2F49;
            }
          }

          & input {
            background: transparent !important;
            box-shadow: none !important;
            backdrop-filter: none;
            padding: 0 16px 8px;
            font-size: 24px;
            line-height: 29px;

            &:focus {

              &~label {
                top: 6px;
              }
            }

            &:disabled~label {
              color: #C3C4D4;
            }
          }

          & label {
            font-size: 24px;
            line-height: 29px;
            left: 16px;
          }
        }

        &__btn {
          flex: auto;
          justify-content: flex-start;
          margin-top: 15px;

          & button {
            display: inline-flex;
            height: 80px;
            font-weight: 600;
            width: 432px;
            align-items: center;
            text-align: center;
            color: #FFFFFF;
            border-radius: 7px 0 35px 7px;
            padding: 1px 38px 0 14px;
            position: relative;
            justify-content: center;
            z-index: 1;
            perspective: 200px;
            font-size: 0;
            @include transition;

            &:before {
              content: ' ';
              font-size: 16px;
              line-height: 19px;
            }

            &:after {
              z-index: -2;
              background: linear-gradient(94.22deg, #F39324 -14.8%, #EC663B 106.65%);
              transform: rotateX(-6deg) skewX(-7deg);
              transform-origin: left top;
              border-radius: 10px 0 29px 10px;
              height: 104%;
              width: 100%;
              position: absolute;
              content: "";
              top: 0;
              left: 0;
              @include transition;
            }

            & svg {
              display: none;
            }
          }
        }
      }
    }
  }
}

