.about {
    padding-bottom: 119px;

    &__hero {
        background-image: url(../img/about/hero.jpg);
        background-size: cover;
        padding: 84px 0 83px;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        margin-top: 39px;

        &:first-of-type {
            margin-top: 0;
        }

        &-left,
        &-right {
            display: flex;
            width: 50%;
        }

        &-right {
            padding-left: 70px;

            &.about__row-text {
                padding-left: 0;
            }
        }

        &-wp {
            max-width: 720px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            &_toline {
                display: flex;
                max-width: 576px;
                width: 100%;

                .about__img {
                    img {
                        width: initial;
                    }
                }

            }
        }

        &-left {
            justify-content: flex-end;
            padding-left: 24px;

            &.about__row-wide {
                padding-right: 72px;
                padding-left: 0;
            }
        }

        &-wide {
            .about__row-wp {
                max-width: initial;
            }
        }

        img {
            width: 100%;
        }

        &-text {
            .about__row-wp {
                max-width: 648px;
            }
        }

        &_info {
            display: block;
            max-width: 1344px;
            padding-left: 24px;
            padding-right: 24px;
            margin-left: auto;
            margin-right: auto;
            max-height: 100%;

            .about__row-left {
                width: 61.3%;
            }

            .about__row-wp {
                max-width: 862px;
            }

            .about__row-right {
                width: 38.6%;
            }

            // columns: 2;

            p {

                // break-inside: avoid;
                // height: 30%;
                &:first-of-type {
                    padding-top: 42px;
                }
            }

            .about__img {
                float: right;
                margin: 0 0 0px 75px;
                // height: 100%;
                // flex-grow: 2;
                // width: 70%;
            }

            &::after {
                content: " ";
                clear: both;
                display: table;
            }


        }

        &_revers {
            .about__row-left {
                padding-right: 72px;

                &:not(.about__row-wide) {
                    .about__img {
                        max-width: 576px;

                        img {
                            margin-right: auto;
                        }
                    }
                }
            }



            &-wide {
                flex-direction: row-reverse;

                .about__row-left.about__row-wide {
                    padding-left: 72px;
                    padding-right: 0;
                }

                .about__row-right.about__row-text {
                    justify-content: flex-end;
                }
            }
        }
    }

    &__img {
        line-height: 0;
        overflow: hidden;
        height: 100%;
        display: flex;
        align-items: flex-start;
        justify-content: flex-end;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
    }

    blockquote {
        position: relative;
        padding-top: 33px;
        padding-left: 72px;
        margin-bottom: 40px;

        .project-double {
            position: absolute;
            top: 0;
            left: 17px;

            span {
                height: 33px;
                width: 12px;
            }
        }

        p {
            font-style: italic;
            font-weight: 500;
            font-size: 24px;
            line-height: 160%;
            color: #2C2F49;
        }
    }

    h5 {
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 120%;
        color: #2C2F49;
        margin-bottom: 24px;
    }

    h3 {
        font-style: normal;
        font-weight: 600;
        font-size: 48px;
        line-height: 120%;
        text-align: center;
        color: #2C2F49;
        margin-bottom: 24px;
    }

    p {
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 163%;
        color: #2C2F49;
        margin-bottom: 16px;

        &:last-of-type {
            margin-bottom: 0;
        }
    }

    &__grey {
        margin-top: 65px;
        display: flex;
        justify-content: center;
        padding: 33px 20px 36px;
        max-width: 1440px;
        margin-left: auto;
        margin-right: auto;
        position: relative;
        z-index: 1;
        perspective: 200px;
        margin-bottom: 78px;

        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 102%;
            z-index: -1;
            background: #F3F4F8;
            transform-origin: center top;
            transform: rotateX(-3.6deg);
            border-radius: 0 0 34px 34px;

        }

        &-wp {
            max-width: 1336px;
            padding-left: 20px;
            padding-right: 20px;
            display: flex;
            justify-content: center;



            & p {
                font-style: italic;
                font-weight: 500;
                font-size: 24px;
                line-height: 160%;
                text-align: center;
                color: #2C2F49;
            }
        }
    }

    &__text {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 65px;

        p {
            max-width: 720px;
            text-align: center;
        }
    }

    @media (max-width: 1360px) {
        &__row {
            &_revers {
                padding-right: 24px;
            }

            &_info {
                .about__img {
                    margin-bottom: 30px;
                }
            }
        }
    }

    @media (max-width: 990px) {

        padding-bottom: 64px;

        &__hero {
            padding: 77px 0 251px;


            .project-double {
                margin-top: 12px;
            }

            .services__hero-title {
                padding-left: 7px;
                width: 49%;
                margin-left: auto;
                margin-right: auto;
                align-items: flex-start;
            }
        }



        &__img {
            width: 75%;
            margin-left: auto;
            margin-right: auto;
            height: 47vw;
            margin-bottom: 41px;
            justify-content: center;
        }

        &__row {
            flex-direction: column-reverse;

            &-wp {
                width: 100%;
                max-width: initial;
                padding-left: 0;

                &_toline {
                    .about__img {
                        img {
                            width: 100%;
                            object-position: center 26%;
                        }
                    }
                }
            }

            &-right {
                padding-left: 0;
            }



            &:first-of-type {
                margin-top: -175px;
                flex-direction: column-reverse;
                display: flex;

                .about__img {
                    width: 100%;
                    height: 100%;
                    margin-bottom: 0;
                }

                .about__row-right {
                    padding-left: 0;
                    justify-content: flex-end;
                    margin-bottom: 38px;

                    .about__row-wp {
                        width: 87.5%;
                    }
                }

                .about__row-left {
                    padding-right: 24px;
                }

                .about__row-text {
                    width: 100%;
                }
            }

            &-left,
            &-right {
                width: 100%;
            }

            &-left {
                &-mob {
                    .about__row-wp {
                        .about__img {
                            margin-left: 0;
                            width: 87.5%;
                            max-width: initial !important;
                        }
                    }
                }

            }

            &-text {
                width: 90.6%;
                margin-left: auto;
                padding-right: 24px;

                .about__row-wp {
                    max-width: initial;
                }
            }

            &_revers {
                flex-direction: column;
                padding-right: 0;

                .about__img {
                    margin-left: auto;
                    width: 75%;
                    margin-right: auto;

                    img {
                        width: 100%;
                    }
                }

                &:last-of-type {
                    .about__row-left {
                        padding-left: 0;
                    }

                    /*.about__img {
                        width: 96.5%;
                    }*/
                }

                .about__row-left {
                    padding-right: 0;
                    padding-left: 0;
                }

                &-wide {
                    flex-direction: column;

                    .about__row-left.about__row-wide {
                        padding-left: 0;

                        .about__img {
                            margin-right: 0;
                            margin-left: auto;
                        }
                    }


                }
            }

            &-left {
                &.about__row-wide {
                    padding-right: 0;
                }
            }

            &-right {
                &.about__row-text {
                    padding-left: 24px;
                }
            }

            &_info {


                // display: flex;
                // display: none;
                // }&::after {

                p {
                    margin-left: auto;
                    max-width: 93%;
                    padding-left: 24px;

                    &:first-of-type {
                        padding-top: 0;
                        margin-bottom: 52px;
                    }
                }

                .about__img {
                    width: 33.4%;
                    height: 49vw;
                    margin: 0 0 30px 48px;
                }
            }

            &-wide {
                .about__img {
                    margin-left: 0;
                    width: 87.5%;
                    margin-right: auto;
                }
            }


        }



        &__text {
            margin-bottom: 40px;
        }

        &__grey {
            padding: 25px 20px 26px;
            width: 90%;
            margin: 41px auto 60px;

            &-wp {
                padding-left: 40px;
                padding-right: 40px;
            }

            &::after {
                transform: rotateX(-6.5deg);
                left: -2%;
                width: 104%;
            }

        }

        h3 {
            font-size: 40px;
            line-height: 120%;
            margin-bottom: 16px;
        }
    }

    @media (max-width: 575px) {

        padding-bottom: 40px;


        &__hero {
            padding-top: 13px;
            padding-bottom: 113px;

            .services__hero-title {
                width: 74.5%;
                align-items: center;
                padding-left: 0;
            }
        }

        &__row {

            margin-top: 23px;


            &:first-of-type {
                margin-top: -89px;

                .about__row-left {
                    padding-right: 26px;
                }

                .about__row-right {
                    margin-bottom: 23px;
                }
            }

            &-left {
                padding-left: 16px;
                padding-right: 24px;

                .about__row-wp {
                    .about__img {
                        width: 100%;
                    }
                }
            }

            &-right {
                &.about__row-text {
                    padding-left: 16px;
                    padding-right: 24px;
                }
            }

            &-text {
                width: 100%;
            }

            &_info {
                padding-left: 0px;
                padding-right: 0px;
                display: block;
                margin-top: 22px;

                .about__img {
                    float: initial;
                    width: 100%;
                    height: 66.8vw;
                    margin-left: 0;
                    margin-bottom: 26px;
                }

                p {
                    padding-left: 16px;
                    padding-right: 16px;
                    max-width: initial;

                    &:first-of-type {
                        margin-bottom: 15px;
                    }
                }
            }

            &_revers {
                &:last-of-type {
                    .about__row-left {
                        padding-right: 0;
                    }

                    .about__img {
                        width: 100%;
                    }
                }

                .about__img {
                    width: 100%;
                }
            }

            &-wide {
                .about__img {
                    width: 100%;
                }
            }
        }

        &__img {
            width: 100%;
            height: 66.8vw;
            margin-bottom: 24px;
        }

        blockquote {
            padding-left: 23px;
            padding-top: 17px;
            margin-bottom: 24px;

            .project-double {
                left: 8px;

                span {
                    height: 16px;
                    width: 6px;
                    border-radius: 2px 0;

                    &+span {
                        margin-left: 4px;
                    }
                }
            }

            p {
                font-size: 16px;
                line-height: 164%;
            }
        }

        p {
            font-size: 12px;
            line-height: 160%;
        }

        &__grey {
            width: 100%;
            background: #F3F4F8;
            padding: 39px 4px 41px;
            margin-top: 40px;
            margin-bottom: 40px;

            &:after {
                display: none;
            }

            p {
                font-size: 16px;
                line-height: 164%;
            }
        }

        h3 {
            font-size: 24px;
            line-height: 120%;
            margin-bottom: 9px;
        }

        h5 {
            font-size: 16px;
            line-height: 19px;
            margin-bottom: 17px;
        }

        &__text {
            padding-left: 16px;
            padding-right: 16px;
            margin-bottom: 24px;
        }




    }



}

.about-resume {
    background: #F3F4F8;
    padding-top: 80px;
    padding-bottom: 80px;

    &__title {
        font-weight: 600;
        font-size: 48px;
        line-height: 58px;
        text-align: center;
        color: #2C2F49;
        margin-bottom: 36px;
    }

    &__row {
        display: flex;
        max-width: 1008px;
        width: 100%;
        align-items: flex-start;
        margin-left: auto;
        margin-right: auto;
        padding-bottom: 40px;
        position: relative;
        z-index: 1;

        &:after {
            content: '';
            position: absolute;
            height: 100%;
            top: 0;
            left: 289px;
            border-right: 2px dashed #C3C4D4;
            z-index: -1;
        }

        &:first-of-type {

            &:after {
                top: 55px;
                height: calc(100% - 55px);
            }
        }

        &:last-of-type {

            &:after {
                height: 55px;
            }

            & .about-resume__year {
                color: #2C2F49;
            }

            & .about-resume__center:after {
                background: #EC663B;
            }
        }
    }

    &__year {
        font-weight: 600;
        font-size: 64px;
        line-height: 77px;
        color: #C3C4D4;
        flex: 0 0 266px;

        & span {
            font-size: 40px;
            margin-left: -4px;
        }
    }

    &__center {
        margin-top: 33px;
        flex: 0 0 48px;
        width: 48px;
        height: 48px;
        border-radius: 50%;
        border: 2px solid #C3C4D4;
        background: white;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;

        &:after {
            content: '';
            position: relative;
            background: #2C2F49;
            width: 16px;
            height: 16px;
            flex: 0 0 16px;
            border-radius: 50%;
        }
    }

    &__info {
        padding-left: 46px;
        padding-top: 33px;
    }

    &__name {
        font-weight: 600;
        font-size: 24px;
        line-height: 38px;
        color: #2C2F49;
    }

    &__text {
        margin-top: 16px;

        & p {
            font-weight: 500;
            font-size: 16px;
            line-height: 26px;
            color: #2C2F49;

            &+p {
                margin-top: 26px;
            }
        }
    }
}

@media (max-width: 990px) {
    .about-resume {
        padding-bottom: 39px;

        &__title {
            font-size: 40px;
            line-height: 48px;
            margin-bottom: 61px;
        }

        &__row {


            &:after {
                left: 215px;
            }
        }

        &__year {
            font-size: 48px;
            line-height: 58px;
            flex: 0 0 192px;

            & span {
                font-size: 25px;
                margin-left: -1px;
            }
        }

        &__center {
            margin-top: 18px;
        }

        &__info {
            padding-left: 47px;
            padding-top: 18px;
        }
    }
}

@media (max-width: 767px) {
    .about-resume {
        padding-top: 40px;

        &__title {
            font-size: 24px;
            line-height: 29px;
            margin-bottom: 24px;
        }

        &__row {
            flex-wrap: wrap;


            &:after {
                left: 11px;
            }

            &:first-of-type:after {
                top: 15px;
                height: calc(100% - 15px);
            }

            &:last-of-type {
                padding-bottom: 0;

                &:after {
                    height: 35px;
                }
            }
        }

        &__year {
            order: 2;
            font-size: 32px;
            line-height: 38px;
            flex: auto;
            margin-left: 24px;

            & span {
                font-size: 16px;
                margin-left: -4px;
            }
        }

        &__center {
            margin-top: 7px;
            order: 1;
            width: 24px;
            height: 24px;
            flex: 0 0 24px;

            &:after {
                width: 6px;
                height: 6px;
                flex: 0 0 6px;
            }
        }

        &__info {
            order: 3;
            flex: 0 0 100%;
            padding-left: 48px;
            padding-top: 1px;
        }

        &__name {
            font-size: 16px;
            line-height: 26px;
        }

        &__text {
            margin-top: 9px;

            & p {
                font-size: 12px;
                line-height: 19px;

                & + p {
                    margin-top: 19px;
                }
            }
        }
    }
}
