.services {
    &__hero {
        background-image: url(../img/services/hero.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        padding-top: 81px;
        padding-bottom: 86px;
        margin-top: 96px;
        width: 100%;

        &-title {
            display: flex;
            justify-content: center;
            align-items: center;
            padding-left: 18px;

            h1,
            h3,
            h2 {
                font-style: normal;
                font-weight: 600;
                font-size: 48px;
                line-height: 57px;
                letter-spacing: 0.02em;
                color: #ffffff;
                margin-left: 38px;
            }
        }

        &-subtitle {
            font-style: normal;
            font-weight: 500;
            font-size: 24px;
            line-height: 28px;
            color: #ffffff;
            margin-top: 24px;
            text-align: center;
        }
    }

    &__top-line {
        padding-left: 32px;
        padding-bottom: 34px;
        border-bottom: 2px solid #F3F4F8;
        margin-bottom: 48px;
    }

    &__base {
        &-wp {
            position: relative;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            max-width: 1480px;
            padding-left: 20px;
            padding-right: 20px;
            margin-left: auto;
            margin-right: auto;
        }

        &-img {
            width: 54.5%;
            margin-left: 60px;

            img {
                width: 100%;
            }
        }

        &-num {
            position: absolute;
            top: 0;
            right: 93px;
            font-style: normal;
            font-weight: 600;
            font-size: 48px;
            line-height: 48px;
            color: #F39323;
            padding-right: 37px;

            // border-right: 1px solid #2C2F49;
            &::after {
                content: "";
                position: absolute;
                width: 2px;
                background: #2C2F49;
                height: 100%;
                top: 0;
                right: 0;
                border-radius: 8px;
            }
        }

        &-info {
            margin-right: 83px;
            width: 29%;
            padding-top: 96px;
            margin-left: auto;
            padding-left: 9px;
            min-width: 430px;

            &-top {
                display: flex;
                align-items: center;
            }

            &-title {
                font-style: normal;
                font-weight: 600;
                font-size: 24px;
                line-height: 29px;
                color: #2C2F49;
                margin-left: 25px;
            }
        }

        &-text {
            margin-top: 24px;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 163%;
            color: #878894;
            margin-bottom: 38px;
        }

        .main-revol__btn {
            margin-top: 40px;

            a {
                padding: 0 54px 1px 48px;

                &::before,
                &::after {
                    transform: rotateX(-14deg) skewX(-5deg);
                }
            }
        }

        &-line {
            display: flex;
            justify-content: space-around;
            width: 100%;
            padding-top: 27px;
            padding-bottom: 29px;
            margin-top: 34px;
            position: relative;
            padding-left: 25px;
            padding-right: 33px;

            &::before {
                content: "";
                position: absolute;
                top: 0;
                left: 2px;
                right: 2px;
                bottom: 0;
                background: #F3F4F8;
                transform: rotateX(-11deg);
                transform-origin: center top;
                border-radius: 0 0 54px 54px;
                z-index: -1;
            }

            &-el {
                padding-left: 36px;
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 160%;
                color: #2C2F49;
                position: relative;

                &::before {
                    content: "";
                    position: absolute;
                    top: 5px;
                    left: 0;
                    height: 12px;
                    width: 12px;
                    background: #F39323;
                }
            }
        }

        &-descr {
            width: 100%;
            display: flex;
            justify-content: space-around;
            margin-top: 64px;
            // padding: 0 40px;

            p {
                width: 40%;
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 163%;
                color: #2C2F49;
            }
        }
    }

    &__terms {
        margin-top: 64px;
        padding-top: 124px;
        padding-bottom: 7px;
        background: #F3F4F8;

        &-wp {
            display: flex;
            justify-content: space-around;
            flex-wrap: wrap;
        }

        &-el {
            padding-top: 91px;
            position: relative;
            width: 30%;
            display: flex;
            flex-direction: column;
            align-items: center;
            max-width: 336px;
            margin-bottom: 124px;
            text-align: center;

            &-title {
                font-style: normal;
                font-weight: 600;
                font-size: 24px;
                line-height: 29px;
                margin-top: 48px;
                color: #2C2F49;
            }

            &-text {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 160%;
                color: #878894;
                margin-top: 24px;
            }


        }

        .services__base-num {
            right: 0;
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 24px;
            padding-right: 25px;
        }

        &+.main-request {
            padding: 120px 0 128px;

            textarea {
                height: 65px;
                resize: none;

                @media (max-width: 760px) {
                    height: 99px;

                    &+label {
                        top: 8px;
                    }
                }
            }
        }
    }


    @media (max-width: 1336px) {

        &__base {
            &-info {
                margin-right: 5px;
                min-width: 420px;
                padding-top: 65px;
                width: 35%;
            }

            &-img {
                margin-left: 0;
            }
        }
    }

    @media (max-width: 1239px) {
        &__hero {
            margin-top: 80px;
        }
    }

    @media (max-width: 1040px) {
        &__terms {
            &-wp {
                //  justify-content: space-between;
            }

            &-el {
                width: 50%;
            }
        }
    }

    @media (max-width: 1140px) {

        &__base {
            &-img {
                width: 50%;
            }

            &-num {
                top: -14px;
                right: 71px;
            }

            &-info {
                padding-top: 40px;
                width: 45%;
            }

            &-text {
                margin-bottom: 30px;
            }

            .main-revol__btn {
                margin-top: 0px;
            }

            &-line {
                padding-left: 8px;
                padding-right: 8px;
                padding-top: 20px;

                &-el {
                    padding-left: 15px;

                    &:before {
                        height: 8px;
                        width: 8px;
                        top: 10px;
                    }
                }
            }
        }
    }

    @media (max-width: 990px) {

        &__hero {
            padding-top: 84px;
            padding-bottom: 276px;

            &-title {
                padding-left: 13px;

                h1,
                h3,
                h2 {
                    font-size: 40px;
                    line-height: 120%;
                    margin-left: 28px;
                    letter-spacing: 0;
                    text-align: center;
                }
            }

            .project-double {
                span {
                    height: 25px;
                    width: 8px;
                }
            }

            &-subtitle {
                max-width: 610px;
                padding-left: 16px;
                padding-right: 16px;
                margin-left: auto;
                margin-right: auto;
            }
        }

        &__top {
            display: none;
        }

        &__base {
            margin-top: -199px;

            &-wp {
                padding-right: 24px;
                padding-left: 24px;
            }

            &-img {
                width: 86%;
                padding-left: 21px;
                margin-left: auto;
                margin-right: auto;
            }

            &-num {
                right: initial;
                left: 13%;
                top: -49px;

                &::after {
                    background: #fff;
                }
            }

            &-line {

                background: #F3F4F8;
                width: 38%;
                padding: 40px 24px;
                display: block;
                min-width: 288px;
                margin-top: 24px;

                &::before {
                    display: none;
                }

                &-el {
                    padding-left: 24px;
                    margin-bottom: 25px;

                    &:last-of-type {
                        margin-bottom: 0;
                    }
                }
            }

            &-info {
                margin-left: 0;
                padding-top: 37px;
                padding-left: 0;
                min-width: 384px;
                width: 52%;
            }

            &-text {
                margin-bottom: 40px;
            }

            &-descr {
                justify-content: space-between;

                p {
                    width: 46.7%;
                }
            }




        }

        &__terms {
            margin-top: 65px;
            padding-top: 109px;

            .services__base-num {
                left: initial;

                // right: -22px;
                &::after {
                    background: #2C2F49;
                }
            }

            &-wp {
                justify-content: space-between;
                padding-bottom: 84px;
            }

            &-el {
                padding-top: 11px;
                width: 50%;
                max-width: 370px;
                margin-bottom: 151px;

                svg {
                    height: 64px;
                }

                &-title {
                    margin-top: 40px;
                }

                &-text {
                    margin-top: 17px;
                    max-width: 313px;
                }

                &:last-of-type,
                &:nth-last-child(2) {
                    margin-bottom: 0;
                }


            }

            &+.main-request {
                padding: 79px 0 80px;
            }
        }

    }

    @media (max-width: 767px) {
        &__hero {
            margin-top: 56px;
        }
    }

    @media (max-width: 760px) {
        &__base {
            &-info {
                width: 90%;
                max-width: initial;
                margin-left: auto;
                margin-right: auto;
            }

            &-line {
                width: 100%;
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;

                &-el {
                    width: 46%;
                }
            }

            &-descr {
                p {
                    width: 48%;
                }
            }
        }

        &__terms {


            &-el {
                margin-bottom: 88px;

                svg {
                    height: 40px;
                }

                &-title {
                    font-size: 16px;
                    line-height: 140%;
                    margin-top: 16px;
                }

                &-text {
                    font-size: 14px;
                }
            }
        }


    }

    @media (max-width: 575px) {

        &__hero {
            padding-top: 24px;
            padding-bottom: 139px;

            &-title {
                flex-direction: column;
                padding-left: 0;

                h1,
                h3,
                h2 {
                    font-size: 24px;
                    line-height: 120%;
                    margin-left: 0;
                    margin-top: 8px;
                }
            }

            &-subtitle {
                font-weight: 500;
                font-size: 16px;
                line-height: 19px;
                margin-top: 17px;
            }

            .project-double {
                span {
                    height: 17px;
                    width: 5px;
                    border-radius: 3px 0;

                    &+span {
                        margin-left: 4px;
                    }
                }
            }
        }

        &__base {
            margin-top: -82px;

            &-wp {
                padding-left: 16px;
                padding-right: 16px;
            }

            &-info {
                min-width: initial;
                width: 83%;
                padding-top: 12px;

                &-top {
                    svg {
                        height: 25px;
                        width: 28px;
                    }
                }

                &-title {
                    font-size: 16px;
                    line-height: 19px;
                    margin-left: 15px;
                }

                .main-revol__btn {
                    a {
                        font-size: 14px;
                        line-height: 17px;
                        padding: 0 39px 1px 27px;
                        height: 47px;

                        &::before,
                        &::after {
                            border-radius: 8px 0 29px 8px;
                        }
                    }

                }
            }

            &-text {
                font-size: 12px;
                margin-top: 16px;
                margin-bottom: 24px;
            }


            &-img {
                width: 89%;
                padding-left: 8px;
            }

            &-num {
                font-size: 24px;
                line-height: 24px;
                left: 14%;
                top: -33px;
                padding-right: 15px;
            }

            &-line {
                margin-top: 41px;
                min-width: initial;
                margin-left: -16px;
                margin-right: -16px;
                width: auto;
                padding: 39px 40px 41px;

                &-el {
                    width: 100%;
                }
            }

            &-descr {
                display: block;
                margin-top: 41px;

                p {
                    width: 100%;
                    font-size: 12px;
                    line-height: 160%;

                    &:not(:last-of-type) {
                        margin-bottom: 16px;
                    }
                }
            }

        }

        &__terms {
            margin-top: 39px;
            padding-top: 77px;

            &-wp {
                padding-bottom: 33px;
            }

            &-el {
                width: 47%;
                margin-bottom: 77px;

                &-text {
                    display: none;
                }
            }

            .services__base-num {
                font-size: 16px;
                line-height: 16px;
                top: -29px;
                padding-right: 15px;


                &::after {
                    width: 1px;
                }
            }

            &+.main-request {
                padding: 64px 0 41px;
            }


        }




    }





}

@media (max-width: 1510px){
    .services__terms+.main-request {
        padding-left: 96px;
    }
}


@media (max-width: 1239px){
    .services__terms+.main-request {
        padding-left: 0;
    }
}
