.result-form {
  margin-top: $headerHeight;
  background: linear-gradient(135deg, #4F5062 2.38%, #3A3A4A 67.04%);
  padding-top: 24px;
  padding-bottom: 22px;

  &__mob {
    display: none;
  }

  & .container {
    display: flex;
    flex-wrap: wrap;
  }

  &__types {

    &-wp {
      border: 4px solid white;
      display: flex;
      flex-direction: column;
      background: white;
      border-radius: 18px;
      position: relative;
      z-index: 1;
    }
  }

  &__radio {
    z-index: 1;
    margin-top: 1px;

    &:first-of-type {
      margin-top: 0;
    }

    &-lighter {
      position: absolute;
      top: 0;
      left: 0;
      height: 28px;
      width: 100%;
      border-radius: 18px;
      background: #EC663B;
      z-index: -1;
      @include transition;
    }


    & input {
      display: none;

      &:checked+span {
        color: white;
      }
    }

    & span {
      width: 100%;
      height: 28px;
      border-radius: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 600;
      font-size: 14px;
      line-height: 17px;
      color: #2C2F49;
      padding: 0 9px;
      cursor: pointer;
      @include transition;
    }
  }

  &__inputs {
    flex: 1 1;
    min-width: 60%;
    display: flex;
    margin-left: 8px;
  }

  &__input {
    border-right: 1px solid #2C2F49;
    height: 64px;
    position: relative;
    flex: 1 1;

    &.hidden {
      display: none;
    }

    .select2-selection {
      background: rgba(255, 255, 255, .3);
    }

    .select2-container--default .select2-selection--single .select2-selection__rendered {
      // color: #FFFFFF;
      margin-top: 3px;
    }

    .select2-container--disabled .select2-selection {
      background: rgba(255, 255, 255, .3);
    }

    &:last-of-type {
      border-right: 0;

      & input {
        border-radius: 0 8px 8px 0;
      }
    }

    &:first-of-type {

      & input {
        border-radius: 8px 0 0 8px;
      }
    }

    &.cityof,
    &.cityfor {
      flex: 1.6 1.6;
    }

    &.cont {

      & label {
        font-size: 14px;
        line-height: 16px;
        color: #C3C4D4;
        top: 19px;
      }

      & input {
        background: white;
      }

      .select2-selection {
        background: #fff;
      }
    }

    & label {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      left: 23px;
      color: #2C2F49;
      cursor: pointer;
      z-index: 2;
      @include transition;

      & sup {
        position: absolute;
        font-size: 11px;
        margin-top: -5px;
      }
    }

    & input {
      height: 100%;
      width: 100%;
      display: block;
      backdrop-filter: blur(8px);
      border: none;
      background: white;
      font-family: Gilroy;
      font-style: normal;
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      padding: 14px 24px 0;
      color: #2C2F49;
      position: relative;
      outline: none;
      @include transition;

      &~label {
        cursor: text;
      }

      &:focus {
        box-shadow: 0 0 0 2px #EC663B;
        background: white;
        z-index: 1;

        &+label {
          font-size: 14px;
          line-height: 16px;
          color: #C3C4D4;
          top: 19px;
        }
      }

      &:disabled {
        background: rgba(255, 255, 255, 0.3);

        &+label {
          color: #FFFFFF;
          cursor: default;
        }
      }
    }

    & .select2-container {
      height: 100%;

      &--open {

        & .select2-selection {
          box-shadow: 0 0 0 2px #EC663B;
          background: white;
          z-index: 1;
        }

        &+label {
          font-size: 12px;
          line-height: 14px;
          color: #C3C4D4;
          top: 19px;
        }
      }

      &--default .select2-selection--single .select2-selection__rendered {
        padding: 0;
        font-family: Gilroy;
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;
        color: #2C2F49;
      }

      &--disabled {
        & .select2-selection {
          background: rgba(255, 255, 255, 0.3);
        }

        &~label {
          color: #FFFFFF;
          cursor: default;
        }
      }
    }

    & .select2-selection {
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      backdrop-filter: blur(8px);
      border: none;
      background: white;
      font-family: Gilroy;
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      padding: 0 24px;
      color: #2C2F49;
      position: relative;
      outline: none;
      border-radius: 0;
      @include transition;

      &__arrow {
        display: none;
      }
    }
  }

  &__btn {
    margin-left: 8px;
    display: inline-flex;
    background: transparent;
    border: 0;
    outline: none;
    height: 64px;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    align-items: center;
    text-align: center;
    color: #FFFFFF;
    border-radius: 7px 0 35px 7px;
    padding: 0 65px 1px 54px;
    position: relative;
    z-index: 1;
    perspective: 200px;
    @include transition;

    &:disabled {
      color: #C3C4D4 !important;
      cursor: not-allowed;

      &:before,
      &:after {
        background: #F3F4F8 !important;
      }
    }

    &:before,
    &:after {
      z-index: -2;
      background: linear-gradient(96.43deg, #F39324 6.46%, #EC663B 110.78%);
      transform: rotateX(-14deg) skewX(-7deg);
      transform-origin: left top;
      border-radius: 7px 0 20px 7px;
      height: 107%;
      width: 100%;
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      @include transition;
    }

    &:before {
      background: #EC663B;
      z-index: -1;
      opacity: 0;
    }

    &:hover {
      color: white;

      &:before {
        opacity: 1;
      }
    }
  }

  &__attemp {
    display: flex;
    align-items: center;
    margin-top: 14px;

    & svg {
      fill: white;
    }

    & span {
      margin-left: 8px;
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      color: #FFFFFF;
    }
  }

  &__neighbors {
    margin-left: auto;
    margin-top: 12px;
  }
}

.default-check {
  display: flex;
  align-items: center;
  cursor: pointer;
  transform: translate3d(0, 0, 0);
  position: relative;
  z-index: 1;

  &:hover {

    & .default-check__text {
      color: #EC663B;
    }
  }

  & input {
    display: none;

    &:checked {

      &~.default-check {


        &__icon {


          &:after {
            border-color: rgba(236, 102, 59, 1);
            left: 100%;
            transform: translate3d(-100%, 0, 0);
          }

          & span:after {
            left: 0;
          }
        }
      }
    }
  }

  &__icon {
    width: 46px;
    height: 24px;
    margin-top: 1px;
    display: flex;
    align-items: center;
    position: relative;

    &:after {
      content: '';
      position: absolute;
      width: 24px;
      height: 24px;
      background: #FFFFFF;
      border: 2px solid #878894;
      border-radius: 16px;
      top: 0;
      left: 0;
      @include transition;
    }

    & span {
      width: 46px;
      height: 16px;
      border-radius: 16px;
      overflow: hidden;
      position: relative;
      transform: translate3d(0, 0, 0);
      will-change: transform;

      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 200%;
        height: 100%;
        transform: translate3d(0, 0, 0);
        background: linear-gradient(90deg, rgba(236, 102, 59, 1) 50%, rgba(135, 136, 148, 1) 50%);
        will-change: transform;
        @include transition;
      }
    }
  }

  &__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
    margin-left: 15px;
    user-select: none;
    @include transition;
  }
}

.result-main {
  background: #F3F4F8;
  padding-top: 48px;
  padding-bottom: 80px;

  & .container {
    display: flex;
    align-items: flex-start;
  }
}

.result-box {
  flex: 1 1;
}

.result-aside {
  position: sticky;
  top: 110px;
  margin-left: 72px;
  flex: 0 0 288px;
  width: 288px;
  border: 1px solid #C3C4D4;
  padding: 31px 28px 32px 31px;

  & .project-double {
    position: absolute;
    top: -9px;
    left: 43px;

    & span {
      height: 24px;
      width: 9px;
      border-radius: 3px 0;

      &+span {
        margin-left: 5px;
      }
    }
  }

  &__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: #2C2F49;
    margin-bottom: 32px;
  }

  &__el {
    border-bottom: 1px solid #C3C4D4;
    margin-top: 16px;
    padding-bottom: 15px;
    margin-right: 2px;

    &:last-of-type {
      padding-bottom: 0;
      border-bottom: 0;
    }
  }

  &__icon {
    display: flex;
    align-items: flex-start;
  }

  &__text {
    margin-top: 16px;
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: #878894;

    a {
      color: #EC663B;

      @media (min-width: 767px) {
        transition: 0.25s;

        &:hover {
          color: #878894;
        }
      }
    }
  }
}

.result-el {
  background: #FFFFFF;
  box-shadow: 0 8px 24px rgba(44, 47, 73, 0.04);
  border-radius: 8px;
  overflow: hidden;
  padding: 24px 40px 32px;
  margin-top: 24px;

  &:first-child {
    margin-top: 0;
  }

  &__head {
    display: flex;
    align-items: flex-start;
  }

  &__info {
    padding-top: 5px;
  }

  &__way {
    font-weight: 600;
    font-size: 32px;
    line-height: 38px;
    color: #32333F;

    & span {
      color: #C3C4D4;
    }
  }

  &__timeway {
    display: flex;
    align-items: center;
    margin-top: 6px;

    & svg {
      fill: #F39323;
    }

    &>span {
      margin-left: 16px;
      color: #F39323;
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;

      & span {
        color: #C3C4D4;
      }
    }
  }

  &__tools {
    margin-left: auto;
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    perspective: 200px;
  }

  &__type {
    max-width: 122px;
    padding-left: 31px;
    padding-top: 8px;

    &.budget {

      &:after {
        background: #1CBF4A;
      }
    }

    &.fast {
      max-width: 105px;

      &:after {
        background: #EC663B;
      }
    }

    &.opt {
      max-width: 143px;
      padding-left: 27px;
      padding-top: 7px;
      margin-right: 7px;

      &:after {
        background: #E6E6EB;
      }

      & span {
        color: #2C2F49;
      }
    }


    &:after {
      z-index: -1;
      transform-origin: left top;
      border-radius: 0 0 0 27px;
      height: 113%;
      width: 170%;
      position: absolute;
      content: "";
      top: 4px;
      left: 0;
      transform: rotateX(-18.4deg);
    }

    & span {
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      color: #FFFFFF;
    }
  }

  &__cost {
    position: relative;
    perspective: 200px;
    height: 64px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 8px;
    z-index: 2;
    width: 196px;

    &:after {
      z-index: -1;
      background: linear-gradient(135deg, #4F5062 2.38%, #3A3A4A 67.04%);
      transform-origin: left top;
      border-radius: 0 0 0 33px;
      height: 125%;
      width: 170%;
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      transform: rotateX(-28.4deg);
    }


    & span {
      font-weight: 600;
      font-size: 40px;
      line-height: 42px;
      color: #FFFFFF;
      margin-right: 15px;

      & sub {
        font-size: 24px;
        line-height: 29px;
        position: relative;
        top: -9px;
        left: 4px;
      }
    }

    & .bell {
      position: relative;
      top: -2px;
      left: 1px;

      & svg {
        fill: #C3C4D4;
      }
    }
  }

  &__bottom {
    display: flex;
  }

  &__mapline {
    flex: 1 1;
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    margin-top: 83px;
    margin-bottom: -31px;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      border-bottom: 2px dashed #C3C4D4;
      z-index: -1;
    }

    &-city {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: -11px;

      &:first-child {
        align-items: flex-start;

        & svg circle {
          fill: #F39323;
        }
      }

      &:last-child {
        align-items: flex-end;

        & svg circle {
          fill: #F39323;
        }
      }

      & span {
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
        text-align: center;
        color: #2C2F49;
        position: absolute;
        margin-top: 40px;
      }
    }

    &-type {
      width: 56px;
      flex: 0 0 56px;
      text-align: center;
      position: relative;
      top: -32px;
      display: flex;
      flex-direction: column;
      align-items: center;

      & span {
        font-weight: 500;
        font-size: 14px;
        line-height: 22px;
        color: #2C2F49;
        position: absolute;
        margin-top: -21px;
        white-space: nowrap;
      }

      & i {
        background: white;
        margin-top: 4px;
        width: 100%;
        height: 56px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }

  &__right {
    margin-left: auto;
    flex: 0 0 367px;
  }

  &__subinfo {
    margin-top: 15px;

    & p {
      margin: 0;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      text-align: right;
      color: #878894;

      & span {
        color: #F39323;
        font-weight: 600;
        font-size: 16px;
      }

      &+p {
        margin-top: 8px;
      }
    }
  }

  &__btn {
    margin-top: 28px;

    & a {
      padding-left: 12px;
      padding-bottom: 2px;
      width: 198px;
      height: 48px;
      margin-left: auto;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 1;
      position: relative;
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      color: #EC663B;
      @include transition;

      &:hover {
        color: white;

        & svg {

          & path {
            fill-opacity: 1;
            stroke: #EC663B;
          }
        }
      }

      & svg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;

        & path {
          fill-opacity: 0;
          fill: #EC663B;
          @include transition;
        }
      }
    }
  }
}

.preview-way {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1500;
  overflow: hidden;
  visibility: hidden;

  &.active {
    visibility: visible;

    & .preview-way {

      &__overlay {
        opacity: 1;
      }

      &__content {
        transform: translateX(0);
      }
    }
  }


  &__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(44, 47, 73, 0.4);
    backdrop-filter: blur(24px);
    opacity: 0;
    @include transition('all', .5s);
  }

  &__content {
    position: absolute;
    z-index: 2;
    top: 0;
    right: 0;
    height: 100%;
    max-width: 90%;
    width: 1464px;
    overflow: auto;
    background: #F3F4F8;
    display: flex;
    align-items: flex-start;
    transform: translateX(100%);
    @include transition('all', .4s);
  }

  &__left {
    background: white;
    flex: 1 1;
    padding: 59px 72px 72px;

    .preview-way__type {
      display: none;
    }

    & ul {
      margin: 0;
      padding: 0;
      list-style: none;

      & li {
        position: relative;
        padding-left: 36px;
        font-weight: 600;
        font-size: 16px;
        line-height: 26px;
        color: #2C2F49;
        margin-bottom: 16px;

        &:after {
          content: "";
          position: absolute;
          top: 4px;
          left: 0;
          width: 12px;
          height: 12px;
          background: #F39323;
        }
      }
    }

    & .preview-way__subinfo {
      margin-top: 38px;

    }
  }

  &__way {
    font-weight: 600;
    font-size: 48px;
    line-height: 57px;
    color: #32333F;

    & span {
      color: #C3C4D4;
    }
  }

  &__timeway {
    display: flex;
    align-items: center;
    margin-top: 9px;

    & svg {
      fill: #F39323;
    }

    &>span {
      margin-left: 16px;
      color: #F39323;
      font-weight: 600;
      font-size: 24px;
      line-height: 29px;

      & span {
        color: #C3C4D4;
      }
    }
  }

  & .result-el__mapline {
    margin-top: 94px;
    margin-right: -1px;
    margin-bottom: 77px;

    &:after {
      top: 3px;
    }

    &-city {

      &:last-child {
        & span+span {
          text-align: right;
          margin-right: 2px;
        }
      }

      & svg {
        width: 30px;
        height: 40px;
      }

      & span {
        font-size: 16px;
        line-height: 26px;
        margin-top: 48px;
        white-space: nowrap;

        &+span {
          font-size: 12px;
          line-height: 19px;
          color: #878894;
          margin-top: 75px;
          max-width: 149px;
          white-space: initial;
          width: 100%;
        }
      }
    }

    &-type {
      width: 64px;
      flex: 0 0 64px;

      & i {
        margin-top: 6px;
      }

      & span {
        font-size: 16px;
        line-height: 26px;
        margin-top: -22px;
      }
    }
  }

  &__detailed {
    border-top: 2px solid #F3F4F8;
    padding-top: 35px;
    position: relative;
    z-index: 1;
    display: none;

    &:after {
      content: '';
      position: absolute;
      top: 50px;
      left: 215px;
      height: calc(100% - 100px);
      border-left: 2px dashed #C3C4D4;
      z-index: -1;
    }

    &-el {
      display: flex;
      margin-bottom: 35px;

      &:first-of-type,
      &:last-of-type {

        & .preview-way__detailed-point circle {
          fill: #F39323;
        }
      }

      &.detailed-go {
        align-items: center;
        margin-top: -26px;
        margin-bottom: 20px;

        & .preview-way__detailed-point {
          padding-top: 0;
        }
      }

      &:last-of-type {
        .preview-way__detailed-point {
          background: #FFFFFF;
          padding-top: 0;
          margin-top: 7px;
        }
      }
    }

    &-date {
      flex: 0 0 143px;


      &>span {
        display: block;

        &:nth-of-type(1) {
          font-weight: 600;
          font-size: 16px;
          line-height: 26px;
          text-align: right;
          color: #878894;

          & span {
            font-size: 24px;
            line-height: 29px;
          }
        }

        &:nth-of-type(2) {
          font-weight: 600;
          font-size: 16px;
          line-height: 22px;
          text-align: right;
          color: #2C2F49;
        }
      }
    }

    &-point {
      flex: 0 0 145px;
      display: flex;
      justify-content: center;
      padding-top: 7px;

      & i {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 48px;
        height: 48px;
        background: white;
      }
    }

    &-info {
      flex: 1 1;
      padding-top: 5px;

      & span {
        display: block;

        &:nth-of-type(1) {
          font-weight: 600;
          font-size: 16px;
          line-height: 26px;
          color: #2C2F49;
        }

        &:nth-of-type(2) {
          font-weight: 500;
          font-size: 16px;
          line-height: 26px;
          color: #878894;
        }
      }
    }

    &-btn {
      position: relative;
      z-index: 1;
      width: 322px;
      height: 65px;
      margin-left: 2px;
      padding-left: 24px;
      padding-top: 1px;
      display: flex;
      align-items: center;

      &:hover {

        &>svg path {
          fill-opacity: 1;
          stroke: #EC663B;
        }

        & i svg {
          fill: white;
        }

        & span {
