            top: 50%;
            transform: translateY(-50%);
            left: 0;

            &:after {
              top: 50%;
              transform: translateY(-50%);
              width: 3px;
              height: 20px;
              left: -1px;
            }

            &:nth-of-type(2) {top: 0; left: 25%;}
            &:nth-of-type(3) {top: 0; left: 50%;}
            &:nth-of-type(4) {top: 0; left: 75%;}
            &:nth-of-type(5) {top: 0; left: 100%;}
          }
        }

        &-slider {
          width: 100%;
          height: 4px;
        }

        & .noUi-handle {
          width: 24px;
          height: 32px;
          top: -14px;
          right: -12px;

          &:before,
          &:after {
            width: 8px;
            height: 1px;
          }

          &:before {
            top: 13px;
            left: 8px;
          }

          &:after {
            top: 18px;
            left: 8px;
          }
        }
      }

      &__inputs {
        margin-top: 26px;
      }
    }

    & .main-request__input {
      width: 100%;

      &:first-of-type {
        margin-bottom: 0;
      }

