.main-hero {
  background: url(../img/main/hero.jpg) center top no-repeat;
  background-size: cover;
  padding-top: 179px;
  padding-bottom: 65px;
  clip-path: ellipse(206% 100% at 50% 0);

  &__title {
    font-weight: 600;
    font-size: 80px;
    line-height: 95px;
    text-align: center;
    letter-spacing: 0.02em;
    color: #FFFFFF;
    margin-top: 25px;
  }

  &__text {
    margin-top: 24px;
    font-weight: 500;
    font-size: 40px;
    line-height: 47px;
    text-align: center;
    color: #FFFFFF;
  }

  &__calc {
    margin-top: 64px;
  }

  &__scroll {
    width: 88px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 65px;
    height: 88px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid rgba(218, 226, 232, 0.15);
    cursor: pointer;

    &:hover svg {
      fill: #EC663B;
    }

    & svg {
      fill: white;
      @include transition;

      &:nth-of-type(2) {
        display: none;
      }
    }
  }

  &__mobileshow {
    display: none;
  }
}

.main-calc {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  &__type {
    display: flex;
    justify-content: center;
    flex: 0 0 100%;

    &-wp {
      border: 2px solid white;
      display: flex;
      justify-content: center;
      background: white;
      border-radius: 50px;
      position: relative;
      z-index: 1;
    }
  }

  &__radio {
    min-width: 129px;
    z-index: 1;

    &-lighter {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      min-width: 60px;
      border-radius: 50px;
      background: #EC663B;
      z-index: -1;
      margin-left: -1px;
      @include transition;
    }


    & input {
      display: none;

      &:checked+span {
        color: white;
      }
    }

    & span {
      width: 100%;
      height: 44px;
      border-radius: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      color: #2C2F49;
      padding: 0 18px;
      cursor: pointer;
      @include transition;
    }
  }

  &__row {
    display: flex;
    flex: 0 0 1008px;
    max-width: 100%;
    margin-top: 32px;

    &.five-item {

      & .main-calc__input {

        &.cityof {
          flex: 0 0 296px;
        }

        &.cityfor {
          flex: 0 0 285px;
        }
      }
    }
  }

  &__input {
    border-right: 1px solid #2C2F49;
    height: 80px;
    position: relative;

    &:last-of-type {
      border-right: 0;

      & input {
        border-radius: 0 8px 8px 0;
      }
    }

    &:first-of-type {

      & .select2-selection,
      & input {
        border-radius: 8px 0 0 8px;
      }


    }

    &.cityof {
      flex: 0 0 329px;
    }

    &.cityfor {
      flex: 0 0 332px;
    }

    &.hidden {
      display: none;
    }

    &.cont {

      & label {
        font-size: 12px;
        line-height: 14px;
        color: #C3C4D4;
        top: 19px;
      }

      & input {
        background: white;
      }
    }

    & label {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      left: 23px;
      color: #2C2F49;
      cursor: pointer;
      z-index: 2;
      @include transition;

      & sup {
        position: absolute;
        font-size: 11px;
        margin-top: -5px;
      }
    }

    & input {
      height: 100%;
      width: 100%;
      display: block;
      backdrop-filter: blur(8px);
      border: none;
      background: white;
      font-family: Gilroy;
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      padding: 0 24px;
      color: #2C2F49;
      position: relative;
      outline: none;
      @include transition;

      &~label {
        cursor: text;
      }

      &:focus {
        box-shadow: 0 0 0 2px #EC663B;
        background: white;
        z-index: 1;

        &+label {
          font-size: 12px;
          line-height: 14px;
          color: #C3C4D4;
          top: 19px;
        }
      }

      &:disabled {
        background: rgba(255, 255, 255, 0.3);

        &+label {
          color: #FFFFFF;
          cursor: default;
        }
      }
    }

    & select {
      height: 100%;
      width: 100%;
      display: block;
      backdrop-filter: blur(8px);
      border: none;
      background: white;
      font-family: Gilroy;
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      padding: 0 24px;
      color: #2C2F49;
      position: relative;
      outline: none;
      @include transition;

      &.cont {
        box-shadow: 0 0 0 2px #EC663B;
        background: white;
        z-index: 1;

        &~label {
          font-size: 12px;
          line-height: 14px;
          color: #C3C4D4;
          top: 19px;
        }
      }

      & option {

        &:empty {
          display: none;
        }
      }
    }

    & .select2-container {
      height: 100%;

      &--open {

        & .select2-selection {
          box-shadow: 0 0 0 2px #EC663B;
          background: white;
          z-index: 1;
        }

        &+label {
          font-size: 12px;
          line-height: 14px;
          color: #C3C4D4;
          top: 19px;
        }
      }

      &--default .select2-selection--single .select2-selection__rendered {
        padding: 0;
        font-family: Gilroy;
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;
        color: #2C2F49;
      }

      &--disabled {
        & .select2-selection {
          background: rgba(255, 255, 255, 0.3);
        }

        &~label {
          color: #FFFFFF;
          cursor: default;
        }
      }
    }

    & .select2-selection {
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      backdrop-filter: blur(8px);
      border: none;
      background: white;
      font-family: Gilroy;
      font-style: normal;
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      padding: 0 24px;
      color: #2C2F49;
      position: relative;
      outline: none;
      border-radius: 0;
      @include transition;

      &__arrow {
        display: none;
      }
    }
  }

  &__btn {
    flex: 0 0 100%;
    display: flex;
    justify-content: center;
    margin-top: 56px;


    & button,
    & a {
      position: relative;
      perspective: 200px;
      z-index: 1;
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      color: #FFFFFF;
      height: 80px;
      padding: 0 55px;
      display: flex;
      align-items: center;
      background: transparent;
      outline: none;
      @include transition;

      &:hover {

        & svg {

          & path {
            fill: #EC663B;
            stroke: #EC663B;
          }
        }
      }

      &:disabled {
        color: #F3F4F8;
        cursor: not-allowed;

        & svg {

          & path {
            stroke: #F3F4F8;
            fill-opacity: 0;
          }
        }

        &:hover {
          color: #878894;

          & svg path {
            stroke: #878894;
          }
        }
      }

      & svg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;

        & path {
          fill-opacity: 1;
          fill: url(#svggradient);
          @include transition;
        }
      }

      /* &:after {
        z-index: -1;
        //background: linear-gradient(90deg, #F39324 14.46%, #EC663B 95.71%);
        border: 2px solid;
        border-image-slice: 1;
        border-image-source: linear-gradient(90deg, #F39324 14.46%, #EC663B 95.71%);
        transform: rotateX(-12deg);
        transform-origin: center top;
        border-radius: 0 0 35px 35px;
        height: 105%;
        width: 100%;
        position: absolute;
        content: '';
        top: 0;
        left: 0;
      }*/
    }
  }
}

.select2-results__option:empty {
  display: none;
}

.select2-container--default .select2-results__option[aria-disabled=true] {
  display: none;
}

