.smart-type {
  padding: 32px 96px 43px 0;
  display: flex;
  align-items: flex-start;
  max-height: 198px;
  position: relative;
  perspective: 200px;
  cursor: pointer;

  &:after {
    z-index: -1;
    background: linear-gradient(-90deg, #F39324 14.46%, #EC663B 95.71%);
    transform-origin: center top;
    border-radius: 0 0 0 60px;
    height: 115%;
    width: 170%;
    position: absolute;
    content: "";
    top: 0;
    left: -115px;
    opacity: 0;
    transform: translateX(100px) rotateX(-12.4deg);
    @include transition;
  }

  &.active {

    &:after {
      opacity: 1;
      transform: translateX(0) rotateX(-12.4deg);
    }

    & .smart-type {

      &__index,
      &__title,
      &__text {
        color: white;
      }

      &__icons {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }

  &__index {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    display: flex;
    align-items: center;
    color: #F39323;
    margin-left: 1px;
    @include transition;
  }

  &__info {
    padding-left: 47px;
  }

  &__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: #2C2F49;
    @include transition;
  }

  &__text {
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: #2C2F49;
    margin-top: 16px;
    @include transition;
  }

  &__icons {
    align-self: center;
    margin-left: 60px;
    flex: 0 0 72px;
    width: 72px;
    opacity: 0;
    transform: translateX(50px);
    @include transition;

    & div {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 16px;
      width: 100%;
      height: 72px;
      border-radius: 50%;
      background: white;

      &:first-of-type {
        margin-top: 10px;
      }
    }
  }
}

.main-years {
  background: url(../img/main/years-bg.jpg) center no-repeat;
  background-size: cover;
  position: relative;
  padding-bottom: 54px;
  perspective: 200px;

  &:after {
    z-index: 1;
