.main-case {
  position: relative;
  padding-top: 80px;
  z-index: 1;
  overflow: hidden;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 84%;
    background: rgba(243, 244, 248, 0.6);
    z-index: -1;
  }

  &__title {
    font-weight: 600;
    font-size: 48px;
    line-height: 58px;
    text-align: center;
    color: #2C2F49;
  }

  &__subtitle {
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    text-align: center;
    margin-top: 24px;
    color: #878894;
  }

  &__box {
    margin-top: 48px;
    padding-top: 120px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    transform: skewX(-15.7deg);
    width: 95%;
    transform-origin: top right;
    border-radius: 0 0 113px 0;
    display: flex;
    justify-content: center;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 130%;
      height: 100%;
      background: url(../img/main/case.jpg) center no-repeat;
      background-size: cover;
      transform: skewX(15.7deg);
      z-index: -1;
    }
  }

  &__el {
    max-width: 864px;
    background: white;
    margin-left: 15%;
    padding: 48px 72px 0;
    transform: skewX(15.7deg);

    &-title {

      & .project-double {

        & span {
          height: 32px;
          width: 12px;
        }
      }

      &>h2,
      &>h3,
      &>h4,
      &>span {
        display: block;
        text-align: center;
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
        color: #2C2F49;
        margin-top: 16px;
      }
    }

    &-text {
      margin-top: 24px;
      font-weight: 500;
      font-size: 16px;
      line-height: 26px;
      text-align: center;
      color: #878894;
    }

    &-btns {
      display: flex;
      justify-content: center;
      margin-top: 48px;

      & a {
        position: relative;
        z-index: 1;
        height: 64px;
        display: flex;
        align-items: center;
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        padding: 0 28px 1px 42px;

        &:nth-of-type(2) {
          padding: 0 43px 1px 34px;
          margin-left: -1px;

          & span {
            color: #EC663B;
          }
        }

        &:hover {

          & span {
            color: white;
          }

          & svg {

            & path {
              fill: #EC663B;
              stroke: #EC663B;

              &:nth-of-type(2) {
                fill-opacity: 1;
              }
            }
          }
        }

        & svg {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          z-index: -1;

          & path {
            @include transition;

            &:nth-of-type(2) {
              fill-opacity: 0;
              z-index: 2;
              position: relative;
            }
          }
        }

        & span {
          color: white;
          @include transition;
        }
      }
    }
  }
}

