.mobcalc {
    display: none;

    .main-calc__input .select2-container--open .select2-selection {
        box-shadow: initial;
    }

    .main-calc__input .select2-container--open+label {
        top: -6px;
    }



    @media (max-width: 767px) {
        display: block;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1000;
        overflow: hidden;
        visibility: hidden;

        &.step1 {

            & .mobcalc__back {
                opacity: 0;
                visibility: hidden;
            }
        }

        &.active {
            visibility: visible;

            .mobcalc__overlay {
                opacity: 1;
            }

            .mobcalc__content {
                transform: translateX(0);
            }
        }

        .main-calc__row {
            margin-top: 2px;
        }


        &__overlay {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            background: rgba(44, 47, 73, .4);
            backdrop-filter: blur(24px);
            opacity: 0;
            transition: .5s linear;
            transition-property: all;
        }

        &__content {
            position: absolute;
            z-index: 2;
            top: 0;
            right: 0;
            height: 100%;
            width: 100%;
            background: #ffffff;
            display: flex;
            align-items: flex-start;
            transform: translateX(100%);
            transition: .4s;
            padding: 56px 40px 40px;

            &-wp {
                position: relative;
                height: 100%;
            }
        }

        &__header {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 56px;
            background: #F3F4F8;
            padding-left: 20px;
            padding-right: 22px;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        &__logo {
            display: flex;
        }

        &__back,
        &__close {
            min-width: 20px;
        }

        &__form {
            height: 100%;
        }

        &__back {
            a {
                display: flex;
                margin-right: auto;
            }

        }

        &__close {
            display: flex;
            min-width: 18px;

            a {
                display: flex;
                margin-left: auto;
            }
        }

        &__logo {
            img {
                height: 24px;
            }
        }

        &__step {
            height: 100%;
            display: none;
            align-items: flex-start;
            flex-direction: column;
            // overflow-y: auto;
            // overflow-x: hidden;
            width: 240px;

            &.step6 {

                & .mobcalc__btn {

                    & svg path {
                        stroke: none;
                        fill: url(#grdient-to-right);
                    }


                    & span {
                        color: white;
                    }



                    &:not(.disabled) {

                        & svg {

                            & path {
                                fill-opacity: 1;
                            }
                        }
                    }
                }
            }

            &.active {
                display: flex;
            }

            &-num {
                margin-top: 25px;
                font-style: normal;
                font-weight: 800;
                font-size: 104px;
                line-height: 127px;
                color: #F3F4F8;
                margin-right: auto;

            }

            &-title {
                margin-top: -8px;
                margin-bottom: 36px;
                font-style: normal;
                font-weight: 600;
                font-size: 24px;
                line-height: 29px;
                letter-spacing: 0.02em;
                color: #2C2F49;
                height: 56px;
                display: flex;
                align-items: flex-end;
            }

            &-content {

                & .main-calc {

                    &__type {

                        &-wp {
                            background: #F3F4F8;
                            backdrop-filter: blur(16px);
                        }
                    }

                    &__radio {
                        min-width: 120px;

                        & span {
                            font-size: 16px;
                            line-height: 19px;
                            height: 48px;
                            padding-bottom: 2px;
                        }

                        &-lighter {
                            border: 2px solid #FFFFFF;
                        }
                    }

                    &__input {
                        flex: auto !important;
                        border-right: 0;
                        width: 240px;
                        height: 46px;
                        margin-top: -12px;

                        .select2 {
                            &:after {
                                content: '';
                                position: absolute;
                                bottom: 0;
                                left: 0;
                                width: 100%;
                                height: 10px;
                                border: 2px solid #C3C4D4;
                                border-top: 0;
                                z-index: 1;
                            }
                        }

                        &_inp {
                            &:after {
                                content: '';
                                position: absolute;
                                bottom: 0;
                                left: 0;
                                width: 100%;
                                height: 10px;
                                border: 2px solid #C3C4D4;
                                border-top: 0;
                                z-index: 1;
                            }
                        }

                        input {
                            &:focus {
                                &+.main-request__input-border {
                                    border-color: #EC663B;
                                }
                            }
                        }

                        &.cont {

                            &:after {
                                border-color: #2C2F49;
                            }

                            & label {
                                display: none;
                            }

                            .main-request__input-border {
                                border-color: #000;
                            }
                        }

                        & input {
                            background: transparent !important;
                            box-shadow: none !important;
                            backdrop-filter: none;
                            padding: 0 8px;
                            font-size: 16px;
                            line-height: 19px;

                            &:focus {

                                &~label {
                                    top: 6px;
                                }
                            }

                            &:disabled~label {
                                color: #C3C4D4;
                            }
                        }

                        & label {
                            font-size: 16px;
                            line-height: 19px;
                            left: 8px;
                            color: #878894;
                        }
                    }
                }
            }

            &-btn {
                margin-top: auto;
            }
        }


        &__btn {
            height: 48px;
            padding-right: 16px;

            &.disabled {

                & span {
                    color: #F3F4F8;
                }

                & svg {

                    & path {
                        stroke: #F3F4F8;
                    }
                }
            }

            svg {
                &:first-of-type {
                    display: block;
                }
            }
        }

    }

    .main-calc__input .select2-container--open {
        &::after {
            border-color: #EC663B;
        }
    }

    .main-calc__input .select2-container--default .select2-selection--single .select2-selection__rendered {
        font-size: 16px;
        line-height: 19px;
    }
}

.select2-container--default .select2-search--dropdown .select2-search__field {
    border: none;
    outline: none;
    padding-left: 20px;
}

.select2-search--dropdown {
    // transform: translateY(-37px);
    margin-right: 40px;
}

.input-group {
    position: relative;
    //z-index: 1;
    // padding-top: 22px;
    margin-top: -2px;
    margin-bottom: 16px;

    &--notempty,
    &--focus {
        .input-group__label {
            top: 7px;
            font-size: 11px;
            color: #3C4751;
            letter-spacing: 0.03em;
        }
    }

    &--focus {
        input {
            border-color: #232323;
            color: #4F5A65;
        }
    }

    &__label {
        position: absolute;
        top: 21px;
        left: 0;
        font-weight: 300;
        font-size: 16px;
        line-height: 20px;
        color: #4F5A65;
        pointer-events: none;
        transition: .25s linear;
        transition-property: all;
        //@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        //  display: none;
        //}
    }

    .suggest-drop {
        position: absolute;
        top: 115%;
        left: 0;
        width: 100%;
        border: 1px solid #EBEFF2;
        padding: 24px 0;
        z-index: 50;
        overflow: auto;
        max-height: 233px;
        display: none;
        background: #F3F4F8;

        &.active {
            display: block;
        }

        a {
            padding: 10px 0px 14px;
            font-size: 16px;
            line-height: 19px;
            display: flex;
            color: #878894;
            font-weight: 400;
            transition: .25s linear;
            transition-property: all;


            &.disabledn {
                display: none !important;
            }

            span {
                color: #EC663B;
            }
        }

        @media (max-width: 480px) {
            left: -40px;
            width: 100vw;
            padding: 13px 40px 24px;
        }


    }

    svg {
        fill: none;
    }

    input,
    textarea {
        color: #232323;
        width: 100%;
        background: none;
        padding: 0 20px 8px 0;

        &.error {
            &~.input-group__error-symbol {
                display: block;
            }
        }

        &:focus {
            &~.main-request__input-border {
                border-color: #EC663B;
            }

            &~.suggest-drop {
                display: block;
            }
        }
    }

    textarea {
        border: none;
        padding: 5px 20px 8px 20px;
    }

    &__error-symbol {
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        color: #EB5757;
        position: absolute;
        right: 5px;
        top: 22px;
        z-index: 1;
        pointer-events: none;
        display: none;
    }

    &__submit {
        background: none;
        outline: none;
        border: none;
        border-radius: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 36px;
        height: 36px;
        position: absolute;
        right: 10px;
        bottom: 9px;

        svg {
            fill: none;
            stroke: #2C2F49;
            @include transition(stroke);
        }

        @media (min-width: 1070px) {
            &:hover {
                svg {
                    stroke: #66707A;
                }
            }
        }
    }

    &__question {
        position: absolute;
        right: -34px;
        top: 50%;
        transform: translateY(-50%);
        background-color: #F1F1F1;
        border-radius: 50%;
        width: 18px;
        height: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        font-weight: 500;
        font-size: 12px;
        line-height: 18px;
        color: #232323;
        cursor: pointer;
    }

    &--simple {
        background-color: transparent;

        input {
            padding-left: 0;
        }
    }

    &--bg-sh,
    &--bg-sh-q {

        .input-group {
            &__label {
                left: 20px;
            }
        }

        input {
            border-color: transparent;
            font-weight: normal;
            padding-left: 20px;
            height: 48px;
        }

        &.input-group--focus {
            input {
                border-color: #232323;
            }
        }
    }

    label.error {
        //opacity: 0;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        color: #4F5A65;
        position: absolute;
        left: 0;
        top: calc(100% + 13px);
        border: 1px solid #C4C4C4;
        background-color: #fff;
        z-index: 2;
        pointer-events: none;
        padding: 14px 18px;

        &::before {
            content: "";
            position: absolute;
            left: 18px;
            border-left: 9px solid transparent;
            border-right: 9px solid transparent;
            border-bottom: 9px solid #C4C4C4;
            top: -9px;
            //border-bottom-color: #C4C4C4;
        }

        &::after {
            content: "";
            position: absolute;
            border-bottom: 9px solid #fff;
            top: -8px;
            left: 18px;
            border-left: 9px solid transparent;
            border-right: 9px solid transparent;
        }
    }

    textarea~.input-group__error-symbol {
        top: 14px;
    }

    .main-request__input-border {}
}

@media (max-width: 375px) {
    .select2-container--open .select2-dropdown--below {
        padding-left: 40px;
        // padding-right: 30px;
        background: #F3F4F8;
        width: 100vw !important;
        margin-left: -40px;
    }
}
