@mixin transition($element: all, $time: .25s, $function: linear) {
	transition: $time $function;
	transition-property: #{$element};
}

@mixin transition-cubic($time: .25s, $function: cubic-bezier(1,0,0,1), $element: all) {
	transition: $element $time $function;
}
.project-double {
	display: flex;
	align-items: center;
	justify-content: center;

	&.white {

		& span {
			background: white;
		}
	}

	& span {
		height: 40px;
		width: 16px;
		transform: skewX(-45deg);
		border-radius: 6px 0;
		background: linear-gradient(0, #F39324 14.46%, #EC663B 95.71%);

		&+span {
			margin-left: 7px;
		}
	}
}
