[data-whatintent='touch'],
[data-whatintent='mouse'] {
  *:focus {
    outline: none;
  }
}

* {
  box-sizing: border-box;
  font-family: Gilroy, sans-serif;

  //outline: none;
  //	@extend %scrollbar;
}

*::before,
*::after {
  box-sizing: inherit;
}

html {
  //touch-action: manipulation;
  min-height: -webkit-fill-available;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  cursor: default;
  min-height: 100vh;
  min-height: -webkit-fill-available;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
ol {
  margin: 0;

  &.no-style {
    padding: 0;
    list-style: none;
  }
}

button {
  border: none;
  cursor: pointer;
}

img {
  border: none;
}

a {
  text-decoration: none;
  color: inherit;
}

[contenteditable=true]:empty:before {
  content: attr(data-placeholder);
  color: #131313;
  font-size: inherit;
  display: block;
  /* For Firefox */
}

select {
  appearance: none;

  &::-ms-expand {
    //  IE
    display: none;
  }
}

input[type='number'] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

blockquote {
  margin: 0;
}


.container {
  max-width: 1336px; //1296
  width: 100%;
  margin: auto;
  padding: 0 20px;

  @media (max-width: 990px) {
    padding: 0 24px;
  }

  @media (max-width: 760px) {
    padding: 0 16px;
  }
}
