.cases {

    &__main {
        padding-bottom: 121px;
    }

    &__hero {
        padding-top: 90px;
        padding-bottom: 100px;
        background-image: url(../img/cases/hero.jpg);

        .services__hero-subtitle {
            max-width: 1220px;
            padding-left: 15px;
            padding-right: 15px;
            margin-top: 40px;
            font-style: normal;
            font-weight: 500;
            font-size: 24px;
            line-height: 160%;
            text-align: center;
            margin-left: auto;
            margin-right: auto;
        }
    }

    &__top {
        .services__top-line {
            display: flex;
            justify-content: space-between;
            margin-bottom: 46px;
        }

        &-left {
            display: flex;
            align-items: flex-end;
            margin-bottom: -7px;

            input {
                height: 33px;
                width: 383px;
                padding-right: 35px;
            }
        }
    }



    &__search {
        position: relative;
        margin-right: 110px;

        svg {
            position: relative;
            z-index: 2;
            cursor: pointer;
            fill: #2C2F49;
            transition: fill 0.3s;

            @media (min-width: 761px) {
                &:hover {
                    fill: #EC663B;
                }
            }
        }

        &-form {
            position: absolute;
            right: -8px;
            top: 0;
            z-index: 0;
        }

        .main-request__input {
            margin-bottom: 0;

            &-border {
                z-index: initial;
            }
        }
    }

    &__link {
        position: relative;
        display: flex;
        align-items: center;
        // justify-content: space-between;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        padding: 0 27px 1px 42px;
        z-index: 1;
        height: 64px;
        min-width: 160px;


        svg {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: -1;

            path {
                transition: 0.25s;
                fill-opacity: 0;
                fill: #fff;
            }

        }

        span {
            transition: 0.25s;
            color: #EC663B;

        }


    }

    &__filter {

        .simple-select {
            &::before {
                position: absolute;
                content: url(../img/cases/mini-arrow.svg);
                right: 31px;
                top: 2px;
                transition: transform 0.2s;
            }

            &.is-active {
                &::before {
                    content: url(../img/cases/mini-arrow_o.svg);
                    transform: rotate(180deg);

                }
            }
        }
    }

    &__grid {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 64px;
    }

    &__item {
        box-sizing: border-box;
        border: 2px solid #F3F4F8;
        width: 50%;
        padding: 39px 48px 45px;
        display: flex;
        flex-direction: column;

        &-bottom {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            margin-top: auto;

            &-left {
                margin-right: 10px;
            }
        }

        &-middle {
            margin-top: 24px;
            margin-bottom: 16px;
        }

        &-top {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-left: 13px;
        }

        &-title {
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 29px;
            color: #2C2F49;
        }

        &-text {
            overflow: hidden;
            text-overflow: ellipsis;
            margin-top: 24px;
            height: 104px;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 163%;
            color: #878894;
        }

        .project-double {
            span {
                height: 32px;
                width: 12px;
            }
        }

        &-date {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #F39323;
        }

        @media (min-width: 761px) {
            &:hover {
                .cases__link {
                    svg {
                        path {
                            fill-opacity: initial;
                            fill: #EC663B;
                            stroke: #EC663B;
                        }
                    }

                    span {
                        color: #fff;
                    }
                }
            }
        }

        &-name {
            margin-bottom: 8px;
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 19px;
            color: #2C2F49;
        }

        &-rank {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 160%;
            color: #878894;
        }

    }


    &__pagination {
        display: flex;
        justify-content: center;

        &-wp {
            justify-content: center;
            display: flex;
            align-items: center;
        }

        &-el {
            display: flex;
            align-items: center;
            justify-content: center;
            box-sizing: border-box;
            border-top: 2px solid #F3F4F8;
            border-bottom: 2px solid #F3F4F8;
            border-left: 1px solid #F3F4F8;
            border-right: 1px solid #F3F4F8;
            width: 56px;
            height: 56px;
            transition: 0.25s;
            color: #2C2F49;

            &.current {
                border-color: transparent;
                background: #EC663B;
                color: #fff;

                svg {
                    fill: #fff;
                }
            }

            svg {
                fill: #2C2F49;
                transition: 0.25s;
            }

            @media (min-width: 575px) {
                &:hover {
                    border-color: transparent;
                    background: #EC663B;
                    color: #fff;

                    svg {
                        fill: #fff;
                    }
                }
            }
        }
    }

    @media (max-width: 990px) {

        &__main {
            padding-bottom: 63px;
        }


        &__top {
            display: block;

            .breadcrumbs {
                display: none;
            }

            &-left {
                margin-bottom: 0px;
                width: 100%;
                flex-direction: row-reverse;
                padding-top: 16px;
                justify-content: space-between;
                padding-top: 31px;

                input {
                    height: 30px;
                }
            }

            .main-request__input {
                width: initial;

                &-border {
                    bottom: -11px;
                }
            }

            .services__top-line {
                padding-left: 24px;
                padding-bottom: 27px;
            }

        }

        &__search {
            margin-right: 8px;

            svg {
                margin-right: 16px;
            }
        }

        &__filter {
            .simple-select {
                &::before {
                    right: 10px;
                    top: 3px;
                }
            }
        }

        &__hero {
            padding-top: 78px;
            padding-bottom: 80px;

            .services__hero-title {
                padding-left: 5px;
            }

            .services__hero-subtitle {
                font-weight: 500;
                font-size: 16px;
                line-height: 164%;
                margin-top: 25px;
                width: 70%;
                padding-left: 0;
                padding-right: 0;
            }
        }

        &__item {
            width: 100%;
            padding: 40px 46px 44px;

            &-date {
                font-size: 14px;
                line-height: 16px;
            }

            &-name {
                font-size: 14px;
                line-height: 17px;
            }

            &-rank {
                font-size: 14px;
                line-height: 160%;
            }
        }

        &__grid {
            margin-bottom: 66px;
        }
    }

    @media (max-width: 575px) {

        &__main {
            padding-bottom: 40px;
        }

        &__top {
            border-bottom: 1px solid #F3F4F8;
            margin-bottom: 15px;

            .services__top-line {
                padding-left: 8px;
                padding-bottom: 21px;
                border: none;
                margin-bottom: 0;
            }

            &-left {
                padding-top: 26px;

                input {
                    width: 100%;
                    height: 32px;
                }
            }

            .main-request__input-border {
                bottom: -6px;
            }
        }

        &__search {

            svg {
                width: 19px;
                height: 21px;
                margin-right: 2px;
            }

            &-form {
                width: 280px;
                top: -9px;
                background: #fff;
                z-index: 1;
            }
        }


        &__hero {
            padding-top: 24px;
            padding-bottom: 39px;

            .services__hero-title {
                padding-left: 0;
            }

            .services__hero-subtitle {
                font-size: 12px;
                line-height: 144%;
                margin-top: 17px;
                width: 90%;

            }
        }

        &__item {
            padding: 39px 22px 20px;

            .project-double {
                margin-left: -5px;

                span {
                    height: 16px;
                    width: 6px;
                    border-radius: 3px 0;

                    &+span {
                        margin-left: 4px;
                    }
                }
            }

            &-title {
                font-size: 16px;
                line-height: 19px;
            }

            &-middle {
                margin-top: 16px;
                margin-bottom: 23px;
            }

            &-text {
                height: initial;
                font-size: 12px;
                line-height: 160%;
                margin-top: 17px;
            }

            &-date {
                font-size: 12px;
                line-height: 14px;
            }

            &-name {
                font-size: 12px;
                line-height: 14px;
                margin-bottom: 5px;
            }

            &-rank {
                font-size: 12px;
                line-height: 160%;
            }


        }

        &__link {
            display: none;
        }

        &__grid {
            margin-bottom: 41px;
        }

        &__pagination {
            &-el {
                width: 48px;
                height: 48px;
                font-size: 12px;
                line-height: 14px;
            }
        }




    }


}