.contacts {

    // padding-bottom: 120px;

    &__hero {
        background-image: url(../img/contacts/hero.jpg);
    }

    &__map {
        width: 56%;
        height: 374px;
        overflow: hidden;

        iframe {
            width: 100%;
        }
    }

    &__all {
        margin-left: 75px;
        padding-top: 53px;
        width: 38.6%;

        &>.contacts__item:first-of-type {
            margin-bottom: 22px;
        }

        svg {
            path {
                fill: #2C2F49;
            }

        }
    }

    &__item {
        display: flex;


        &-ico {
            min-width: 28px;
            margin-right: 8px;
            margin-top: 6px;
        }

        span {
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 154%;
            color: #2C2F49;
            max-width: 343px;
        }
    }

    &__mess {
        margin-top: 40px;
        columns: 2;
        column-gap: 72px;

        .contacts__item {
            align-items: center;
            margin-bottom: 24px;
            white-space: nowrap;

            &:first-of-type {
                span {
                    text-decoration: none;

                }
            }

            svg {
                path {
                    transition: 0.25s;
                }
            }

            span {
                line-height: 160%;
                transition: 0.25s;
            }

            span {
                text-decoration: underline;
            }

            @media (min-width:767px) {
                &:hover {
                    span {
                        color: #EC663B;
                    }

                    svg {
                        path {
                            fill: #EC663B;
                        }
                    }
                }
            }
        }

    }

    &__msk {
        display: flex;
    }

    &__other {
        display: flex;
        justify-content: space-between;
        margin-top: 80px;


        &-left {
            width: 46.5%;
        }

        &-right {
            width: 44.5%;
        }

        &-title {
            font-style: normal;
            font-weight: 600;
            font-size: 48px;
            line-height: 120%;
            color: #2C2F49;
            margin-bottom: 33px;
        }

        &-rek {
            font-style: normal;
            font-weight: 600;
            font-size: 24px;
            line-height: 160%;
            color: #2C2F49;
            margin-top: 33px;
        }

        &-grid {

            h5 {
                font-style: normal;
                font-weight: 600;
                font-size: 24px;
                line-height: 29px;
                color: #2C2F49;
            }

            &-wp {
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                margin-top: 24px;
            }
        }

        &-el {
            margin-bottom: 36px;
            width: 45%;
            max-width: 244px;

            &-top {
                display: flex;
                align-items: center;
                margin-bottom: 16px;

                img {
                    max-width: 64px;
                    margin-right: 24px;
                }
            }

            .contacts__item {
                margin-bottom: 8px;
                padding-left: 2px;

                &:last-of-type {
                    margin-bottom: 0;
                }
            }

            .contacts__item-ico {
                display: flex;
                align-items: center;
                margin-right: 2px;
                margin-top: 0;
            }

            span {
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 160%;
                color: #2C2F49;
            }
        }

        &-name {
            font-style: normal;
            font-weight: 500;
            font-size: 24px;
            line-height: 28px;
            color: #878894;
        }
    }

    .main-request {
        padding-top: 42px;

        &__btn {
            button {
                padding: 0 165px;
            }
        }
    }

    #contactmap {
        height: 100%;
    }

    @media (max-width: 1260px) {

        &__all {
            margin-left: 20px;
            width: 43%;
        }
    }

    @media (max-width: 1140px) {

        &__msk {
            display: block;
        }

        &__map {
            width: 100%;
        }
    }

    @media (max-width: 990px) {

        margin-top: -223px;

        .container {
            padding-right: 0;
        }

        &__map {
            height: 366px;
            margin-left: auto;
            width: 90%;
        }

        &__all {
            margin-left: auto;
            width: 90%;
            padding-top: 66px;
        }

        &__item {
            span {
                max-width: initial;
            }

            &:first-of-type {
                margin-bottom: 24px;
            }
        }

        &__mess {
            margin-top: 24px;
            column-gap: 15px;
            width: 85%;

            .contacts__item {
                max-width: 300px;
            }
        }

        &__hero {
            padding-top: 76px;
            padding-bottom: 300px;
        }

        &__other {
            margin-top: 56px;
            width: 90.3%;
            margin-left: auto;
            display: block;
            padding-right: 16px;

            &-title {
                font-size: 32px;
                line-height: 120%;
                margin-bottom: 37px;
            }

            &-left,
            &-right {
                width: 100%;
            }

            &-grid {
                max-width: 558px;


            }

            &-right {
                margin-top: 36px;
            }

            &-rek {
                font-size: 24px;
                line-height: 29px;
                max-width: 550px;
            }
        }

        .main-request {
            padding-top: 80px;

            .container {
                padding-right: 24px;
            }

            &__input {

                textarea {
                    height: 190px;
                }


                &.full-width {
                    margin-top: 0;
                }
            }

            &__btn {
                margin-top: 10px;

                button {
                    padding: 0 126px;

                    &::before,
                    &::after {
                        transform: rotateX(-15deg);
                        border-radius: 0 0 25px 25px;
                        height: 109%;
                    }
                }
            }
        }
    }

    @media (max-width: 760px) {
        &__mess {
            columns: initial;

            .contacts__item {
                span {
                    text-decoration: none;
                }
            }
        }

        .main-request__input {
            textarea {
                height: 103px;
                resize: none;
            }
        }
    }

    @media (max-width: 575px) {
        margin-top: -79px;

        &__hero {
            padding-top: 24px;
            padding-bottom: 104px;
        }

        &__map {
            width: 92%;
            height: 57.5vw;
        }

        &__all {
            width: 92%;
            padding-top: 41px;
            padding-right: 45px;

            .contacts__item:first-of-type {
                margin-bottom: 18px;
            }
        }

        &__item {

            &-ico {
                margin-top: -1px;
                margin-right: 3px;
            }

            span {
                font-size: 14px;
                line-height: 152%;
            }

            &:first-of-type {
                svg {
                    width: 12px;
                }
            }

            svg {
                width: 16px;
            }
        }

        &__mess {
            margin-top: 20px;

            .contacts__item {
                margin-bottom: 19px;

                &:first-of-type {
                    margin-bottom: 24px;
                }
            }
        }

        &__other {
            width: 100%;
            padding-left: 0px;
            padding-right: 16px;
            margin-top: 52px;

            &-title {
                font-size: 24px;
                line-height: 120%;
                margin-bottom: 24px;
            }

            &-grid {
                h5 {
                    font-size: 16px;
                    line-height: 19px;
                }
            }

            &-name {
                font-size: 14px;
                line-height: 16px;
            }

            &-el {
                width: 47%;
                margin-bottom: 25px;

                &-top {
                    margin-bottom: 19px;

                    img {
                        max-width: 48px;
                        margin-right: 11px;
                    }
                }

                .contacts__item {
                    margin-bottom: 9px;
                }
            }

            .contacts__item {
                span {
                    font-size: 12px;
                    line-height: 160%;
                }

                &-ico {
                    min-width: 15px;
                }

                svg {
                    width: 9px;
                }

                &:nth-of-type(3) {
                    svg {
                        width: 12px;
                    }
                }
            }

            &-right {
                margin-top: 18px;
            }

            &-rek {
                font-size: 14px;
                line-height: 160%;
                margin-top: 25px;
            }
        }

        .main-request {
            padding-top: 78px;
            padding-bottom: 41px;

            .container {
                padding-right: 16px;
            }

            &__subtitle {
                padding-left: 15px;
                padding-right: 15px;
            }

            &__form {
                margin-top: 12px;
            }

            &__input {
                margin-bottom: 23px;
            }

            &__btn {
                margin-top: 1px;

                button {
                    padding: 0 84px;
                    height: 55px;
                }
            }
        }


    }

}