.footer {
  background: linear-gradient(135deg, #4F5062 2.38%, #3A3A4A 67.04%);
  padding-top: 77px;

  &__top {
    display: flex;
    justify-content: space-between;
  }

  &__left {
    flex: 0 0 360px;
  }

  &__logo {
    display: flex;
    align-items: center;
    width: 231px;

    & img {

    }

    &-text {
      margin-left: 23px;
      font-weight: bold;
      font-size: 11px;
      line-height: 18px;
      padding-top: 1px;
      letter-spacing: 0.08em;
      text-transform: uppercase;
      color: #FFFFFF;
      @include transition;
    }
  }

  &__recall {

    & .main-request__input {
      margin-top: 74px;

      & input {
        color: white;
      }

      &.cont .main-request__input-border {
        border-color: white;
      }
    }

    &-btn {
      height: 64px;
      display: inline-flex;
      align-items: center;
      position: relative;
      z-index: 1;
      border: 0;
      outline: none;
      background: transparent;
      padding: 0 93px 1px 81px;
      margin-top: -9px;

      @media (min-width: 767px) {
        &:hover {
          & svg {

            & path {
              stroke: #EC663B;
              fill-opacity: 1;
            }
          }

          & span {
            color: white;
          }
        }
      }

      & svg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;

        &:nth-of-type(2) {
          display: none;
        }

        & path {
          @include transition;
          fill: #EC663B;
          fill-opacity: 0;
        }
      }

      & span {
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        color: #F39323;
        white-space: nowrap;
        @include transition;
      }
    }
  }

  &__nav {
    padding-top: 4px;
    flex: 0 0 504px;

    &:last-of-type {
      flex: 0 0 144px;

      & nav {
        column-count: 1;
      }
    }

    & nav {
      column-count: 2;
      column-gap: 71px;

      & a {
        display: block;
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        color: #DAE2E8;
        margin-bottom: 48px;
        break-inside: avoid;
        @include transition;

        &:hover {
          color: #EC663B;
        }
      }
    }
  }

  &__title {
    display: flex;
    align-items: flex-start;
    padding-top: 2px;
    padding-left: 7px;
    margin-bottom: 48px;

    & .project-double {
      position: relative;
      top: -1px;

      & span {
        height: 16px;
        width: 6px;
        border-radius: 2px 0;

        &+span {
          margin-left: 3px;
        }
      }
    }

    &>span {
      font-weight: 600;
      font-size: 16px;
      line-height: 19px;
      color: #F39323;
      padding-top: 10px;
      padding-left: 8px;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    height: 74px;
    margin-top: 15px;
    padding-top: 6px;
  }

  &__cop {
    flex: 0 0 360px;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: rgba(135, 136, 148, 0.6);
  }

  &__policy {
    flex: 0 0 504px;

    & a {
      font-weight: 500;
      font-size: 12px;
      line-height: 14px;
      color: rgba(135, 136, 148, 0.6);
    }
  }

  &__studio {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    flex: 0 0 144px;

    & a {
      display: flex;
    }

    & path {
      @include transition;
    }

    &:hover {

      & path {
        fill: white;
      }
    }
  }
}

@media (max-width: 1510px) {
  .footer {
    padding-left: $headerHeight;
  }
}

@media (max-width: 1239px){
  .footer {
    padding: 40px 0 0 0;

    &__logo {
      flex-wrap: wrap;
      justify-content: center;
      width: 100%;

      & img {
        height: 24px;
      }

      &-text {
        flex: 0 0 100%;
        margin-left: 0;
        font-size: 9px;
        line-height: 14px;
        text-align: center;
        margin-top: 15px;
      }
    }

    &__bottom,
    &__top {
      max-width: 576px;
      width: 100%;
      margin: 0 auto;
    }

    &__nav {
      display: none;
    }

    &__left {
      flex: 0 0 100%;
      padding-bottom: 21px;
    }

    &__recall {
      display: flex;
      align-items: center;
      margin-top: 40px;

      & .main-request__input {
        margin: -10px 44px 0 0;
        flex: 1 1;

        & label {
          font-size: 16px;
          line-height: 19px;
          top: 8px;
          left: 7px;
        }

        & input {
          height: 48px;
          font-size: 16px;
          line-height: 19px;
          padding: 0 7px;
        }
      }

      &-btn {
        height: 52px;
        width: 246px;
        flex: 0 0 246px;
        padding: 0 11px 1px 0;
        justify-content: center;
        margin-top: -2px;
        margin-right: -3px;

        & svg {

          &:nth-of-type(1) {
            display: none;
          }

          &:nth-of-type(2) {
            display: block;
          }
        }

        & span {
          font-size: 16px;
          line-height: 19px;
        }
      }
    }

    &__bottom {
      height: 70px;
      padding-top: 0;
    }

    &__cop {
      flex: auto;
    }

    &__policy {
      flex: auto;
      margin-left: 36px;
      margin-top: -2px;
    }

    &__studio {
      flex: auto;

      & svg {
        width: 40px;
        height: 22px;
      }
    }
  }
}

@media (max-width: 767px) {
  .footer {


    &__recall {
      flex-wrap: wrap;
      flex-direction: column;
      align-items: center;
      margin-top: 34px;

      & .main-request__input {
        width: 240px;
        margin-right: 0;

        & label {
          top: 16px;
        }
      }

      &-btn {
        flex: auto;
        margin-top: 21px;
        margin-left: -2px;
        padding-top: 2px;

        & span {
          font-size: 14px;
          line-height: 17px;
        }
      }
    }

    &__bottom {
      height: auto;
      flex-wrap: wrap;
      margin: 0 24px;
      width: calc(100% - 48px);
      padding-top: 14px;
      padding-bottom: 15px;
    }

    &__cop {
      order: 2;
      font-size: 9px;
      line-height: 11px;
    }

    &__policy {
      order: 1;
      flex: 0 0 100%;
      margin-left: 0;
      margin-bottom: 23px;
    }

    &__studio {
      order: 3;
    }
  }
}
