.politic {
    &__wp {
        display: flex;
        justify-content: space-between;
        padding-top: 39px;
    }

    h5 {
        margin-top: 45px;
        margin-bottom: 28px;
    }

    p {
        margin-bottom: 27px;
    }

    &__left {
        width: 67%;
        padding-top: 10px;
    }

    &__right {
        width: 22%;
        min-width: 288px;
        margin-left: 20px;

        .project-double {
            width: 48px;

            span {
                height: 24px;
                width: 8px;
                border-radius: 3px 0;

                &+span {
                    margin-left: 6px;
                }
            }
        }

        a,
        li {
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 19px;
            color: #878894;
        }

        li {
            &.current {
                a {
                    color: #2C2F49;
                }

                color: #2C2F49;
            }

            @media (max-width: 990px) {
                font-size: 12px;
                line-height: 160%;
            }
        }

        a {
            display: block;
            padding-left: 16px;
            margin-bottom: 16px;
            transition: color 0.25s;

            @media (max-width: 990px) {
                font-size: 12px;
                line-height: 160%;
                padding-left: 6px;
                margin-bottom: 8px;
            }



            @media (min-width: 991px) {
                &:hover {
                    color: #2C2F49;
                }
            }
        }

        &-links {
            padding-left: 16px;
            margin-top: 25px;
        }
    }

    @media (max-width: 990px) {
        &__right {
            min-width: 220px;
        }

        &__left {
            padding-top: 0px;
        }

        &__hero {
            padding: 77px 0 76px;

            .services__hero-title {
                width: 100%;
                padding-left: 16px;
                padding-right: 16px;
            }
        }

        &__wp {
            padding-top: 30px;
        }



    }

    @media (max-width: 768px) {

        &__right,
        &__left {
            width: 100%;
            margin-left: 0;
        }

        &__right {
            li {
                &.current {
                    color: #878894;

                    a {
                        color: #878894;
                    }
                }
            }
        }

        &__wp {
            flex-direction: column-reverse;
        }


    }

    @media (max-width: 575px) {

        &__hero {
            padding: 24px 0 28px;
        }

        h5 {
            margin-bottom: 18px;
            margin-top: 30px;
        }

        p {
            margin-bottom: 13px;
        }
    }


}